import React, { PropTypes } from 'react';
import find from 'lodash/find';
import { List } from 'immutable';
import cx from 'classnames';

import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import Details from './details';
import Number from './number';
import Actions from './Actions';
import Block from 'ui/oneui/block';
import NumberForm from './number-form';
import Loading from 'ui/loading';

import {
  loadAgreement,
  insertNumber,
} from 'app/actions/tele/agreement';

class AgreementForm extends React.Component {
  static propTypes = {
    agreement: PropTypes.object.isRequired,
    customers: PropTypes.array.isRequired,
    products: PropTypes.array.isRequired,
    onChange: PropTypes.func.isRequired,
  }

  static storeActions = {
    loadAgreement,
    insertNumber,
  }

  state = {
    showNumberForm: false,
  }

  handleCustomerChange = (e) => {
    const customer = find(this.props.customers, { number: e.target.value });
    const fields = {
      navCustomerId: customer.number,
      navCustomerName: customer.name,
    };

    this.props.onChange(e, fields);
  }

  handleAddNumberForm = () => {
    this.setState({
      showNumberForm: true,
    });
  }

  handleNumberFormOnSave = (e, number) => {
    e.preventDefault();
    if (number.description && number.numbers) {
      this.props.actions.insertNumber(number);
      this.handleNumberFormOnDismiss();
    }
  }

  handleNumberFormOnDismiss = () => {
    this.setState({
      showNumberForm: false,
    });
  }

  render() {
    const { agreement, customers } = this.props;

    let numberForm;
    if (this.state.showNumberForm) {
      numberForm = (
        <NumberForm
          onSave={this.handleNumberFormOnSave}
          onDismiss={this.handleNumberFormOnDismiss}
          number={{
            teleAgreementId: agreement.id,
            products: List(),
          }}
        />
      );
    }

    let activity = null;
    if (this.props.agreement.posting || this.props.agreement.fetching) {
      activity = <Loading />;
    }

    return (
      <div className="agreement-form">
        {activity}
        {numberForm}
        <div className="content form-horizontal">
          <Details
            navCustomerId={agreement.navCustomerId}
            description={agreement.description}
            customers={customers}
            onChange={this.props.onChange}
            onCustomerChange={this.handleCustomerChange}
          />
          {
            agreement.numbers.map((n, i) =>
              <Number
                key={i}
                agreementId={agreement.id}
                number={n}
                products={this.props.products}
                onChange={this.props.onChange}
              />
            )
          }

          <Block>
            <div className="text-center">
              <button
                onClick={this.handleAddNumberForm}
                className="btn btn-default btn-lg"
              >
                Add Number Series
              </button>
            </div>
          </Block>

          <Actions
            disabled={activity != null}
            onReset={this.props.onReset}
            onSubmit={this.props.onSubmit}
          />
        </div>
      </div>
    );
  }
}

export default compose(
  AgreementForm,
  isConnectable
);



