import React, { PropTypes } from 'react';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import isConnectable from 'app/concerns/connectable';
import map from 'lodash/map';
import { loadAgreement, cancelAgreement } from 'app/actions/tele/agreement';
import { push } from 'app/actions/routing';
import Loading from 'ui/loading';

import Actions from './actions';
import Details from './details';
import History from './history';
import Number from './number';

class TeleAgreementsShow extends React.Component {
  static stylesheet = require('../styles.scss');

  static storeProps = {
    agreement: v => v.teleAgreement,
  }

  static storeActions = {
    loadAgreement,
    cancelAgreement,
  }

  static propTypes = {
    actions: PropTypes.object.isRequired,
    agreement: PropTypes.object.isRequired,
    params: PropTypes.shape({
      id: PropTypes.any.isRequired,
    }),
  }

  componentWillMount() {
    const id = parseInt(this.props.params.id, 10);
    this.props.actions.loadAgreement(id);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.agreement.id && !nextProps.agreement.id) {
      push('/tele/agreements');
    }
  }

  handleCancelAgreement = () => {
    const id = parseInt(this.props.params.id, 10);
    this.props.actions.cancelAgreement(id);
  }

  render() {
    const agreement = this.props.agreement;

    let loading;
    if (agreement.posting) {
      loading = <Loading message="Please wait ..." />;
    }

    return (
      <div className="agreement">
        {loading}
        <div className="content">
          <Actions
            id={agreement.id}
            onCancel={this.handleCancelAgreement}
          />

          <Details
            agreement={agreement}
          />

          { agreement.numbers.map(n => <Number key={n.id} {...n} />) }

          <History
            audits={agreement.auditHistory}
          />
        </div>
      </div>
    );
  }
}

export default compose(
  TeleAgreementsShow,
  isStyleable,
  isConnectable
);



