import React, { PropTypes } from 'react';
import Transition from 'react-addons-css-transition-group';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import isStyleable from 'app/concerns/styleable';
import SideBar from 'ui/sidebar';
import Loading from 'ui/loading';
import { hydrateNavision } from 'app/actions/navision';

/**
 * App component
 * @extend {ReactComponent}
 */
class App extends React.Component {
  static stylesheet = require('./styles.scss');

  static storeProps = {
    navision: null,
  }

  static storeActions = {
    hydrateNavision,
  }

  static propTypes = {
    navision: PropTypes.shape({
      hydrated: PropTypes.bool,
    }),
    actions: PropTypes.object,
    children: PropTypes.any,
  }

  componentWillMount() {
    if (!this.props.navision.hydrated) {
      this.props.actions.hydrateNavision();
    }
  }

  render() {
    if (!this.props.navision.hydrated) {
      return <Loading noFadeIn />;
    }

    const transition = {
      enter: App.stylesheet['scene-change-enter'],
      enterActive: App.stylesheet['scene-change-enter--active'],
      leave: App.stylesheet['scene-change-leave'],
      leaveActive: App.stylesheet['scene-change-leave--active'],
    };

    return (
      <div id="page-container" className="sidebar-l sidebar-o side-scroll">
        <SideBar />
        <main id="main-container" className="main">
          <Transition
            transitionName={transition}
            transitionEnterTimeout={200}
            transitionLeaveTimeout={200}
          >
            {React.Children.map(this.props.children, c =>
              React.cloneElement(c, { key: c.props.location.pathname })
            )}
          </Transition>
        </main>
      </div>
    );
  }
}

// Export the composed component
export default compose(
  App,
  isStyleable,
  isConnectable
);



