import React, { PropTypes } from 'react';
import Block from 'ui/oneui/block';

export default function SubscriptionDetails({ subscription }) {
  return (
    <Block
      title="Subscription"
      titleClassName="bg-gray-lighter"
    >
      <div className="block-content">
        <table className="table table-borderless">
          <tbody>
            <tr>
              <td colSpan={2} className="h4 font-w700">
                {subscription.description}
              </td>
            </tr>
            <tr>
              <td className="text-muted" width={120}>Customer</td>
              <td><b>{subscription.navCustomerName}</b></td>
            </tr>
            <tr>
              <td className="text-muted" width={120}>Billing Cycle</td>
              <td><b>{subscription.billingCycle}</b></td>
            </tr>
            <tr>
              <td className="text-muted" width={120}>First Invoice</td>
              <td>
                <b>{subscription.firstInvoice.format('YYYY-MM-DD')}</b>
              </td>
            </tr>
            <tr>
              <td className="text-muted" width={120}>Next Invoice</td>
              <td>
                <b>{subscription.nextInvoice.format('YYYY-MM-DD')}</b>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </Block>
  );
}

SubscriptionDetails.propTypes = {
  subscription: PropTypes.shape({
    description: PropTypes.string.isRequired,
    navCustomerName: PropTypes.string.isRequired,
    billingCycle: PropTypes.string.isRequired,
    firstInvoice: PropTypes.object.isRequired,
    nextInvoice: PropTypes.object.isRequired,
  }),
};



