import React, { PropTypes } from 'react';
import { List } from 'immutable';
import map from 'lodash/map';
import reduce from 'lodash/reduce';
import flatten from 'lodash/flatten';

import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import isConnectable from 'app/concerns/connectable';
import Block from 'ui/oneui/block';
import Grid from 'ui/oneui/grid';
import Icon from 'ui/icon';
import Entry from './entry';

/**
 * TeleAgreements component
 * @extend {ReactComponent}
 */
class TeleAgreements extends React.Component {
  static stylesheet = require('../styles.scss');

  static propTypes = {
    agreements: PropTypes.instanceOf(List).isRequired,
  }

  static storeProps = {
    agreements: s => s.teleAgreements.entries,
  }

  shouldComponentUpdate(nextProps) {
    return !this.props.agreements.equals(nextProps.agreements);
  }

  render() {
    const sum = (arr) => reduce(arr, (a, b) => a + b) || 0;
    const agreements = this.props.agreements.toJS();
    const numberSeries = sum(map(agreements, a => a.numbers.length));
    const products = sum(flatten(map(agreements, a => map(a.numbers, n => n.products.length))));

    return (
      <div className="list-container">
        <div className="content">
          <Grid columns={4} size="sm">
            <Block link="/tele/agreements/new" className="text-center font-w700">
              <div className="text-success"><Icon name="add" size={43} /></div>
              <div
                className="block-content block-content-full
                block-content-mini bg-gray-lighter text-success"
              >
                Add New <span className="hidden-xs hidden-sm hidden-md">Agreement</span>
              </div>
            </Block>

            <Block className="text-center font-w700">
              <div className="h1">{agreements.length}</div>
              <div className="block-content block-content-full block-content-mini bg-gray-lighter">
                Agreements
              </div>
            </Block>

            <Block className="font-w700 text-center">
              <div className="h1">{numberSeries}</div>
              <div className="block-content block-content-full block-content-mini bg-gray-lighter">
                Number Series
              </div>
            </Block>

            <Block className="text-center font-w700">
              <div className="h1">{products}</div>
              <div className="block-content block-content-full block-content-mini bg-gray-lighter">
                Products
              </div>
            </Block>
          </Grid>

          <Block
            header="bg-gray-lighter"
            title={`${agreements.length} Agreements`}
          >
            <table className="table table-borderless table-striped table-vcenter">
              <thead>
                <tr>
                  <th>Client</th>
                  <th>Description</th>
                  <th className="text-center">Number Series</th>
                </tr>
              </thead>
              <tbody>
                { map(agreements, a => <Entry key={a.id} agreement={a} />) }
              </tbody>
            </table>
          </Block>
        </div>
      </div>
    );
  }
}

export default compose(
  TeleAgreements,
  isStyleable,
  isConnectable
);



