import React from 'react';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';

const ICON_SIZES = {
  sm: 18,
  md: 24,
  lg: 36,
  xl: 48,
};

/**
 * Icon component
 * @extends {ReactComponent}
 */
class Icon extends React.Component {
  static stylesheet = require('./icon.scss');

  static propTypes = {
    name: React.PropTypes.string.isRequired,
    className: React.PropTypes.string,
    size: React.PropTypes.any,
  }

  static defaultProps = {
    name: 'star',
    size: 0,
  }

  render() {
    const { className, name, size } = this.props;

    const styles = {};
    if (size) {
      styles.fontSize = ICON_SIZES[size] ? ICON_SIZES[size] : size;
    }

    return (
      <i
        style={styles}
        className={['icon', className]}
      >
        {name}
      </i>
    );
  }
}

// Export the composed component
export default compose(
  Icon,
  isStyleable
);



