import k from 'app/constants';
import { List } from 'immutable';

/**
 * Define the shape of the reducers state
 * @type {FlowType}
 */
type State = {
  history: List,
  location: ?Object,
};

/**
 * Define the initial/clean state for the reducer
 * @type {FlowType}
 */
const initialState: State = {
  history: List(),
  location: null,
};

/**
 * routing reducer
 * @param  {Object} state
 * @param  {Object} action
 * @return {Object}
 */
export default function reducer(state: State = initialState, action: Object): State {
  switch (action.type) {
    case k.ROUTING_LOCATION_CHANGE: {
      const history = state.history.push(action.location);
      return { ...state, history, location: action.location };
    }

    default: {
      return state;
    }
  }
}




