import React, { PropTypes } from 'react';
import { fromJS, List } from 'immutable';

import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import PageHeader from 'ui/oneui/page-header';
import AgreementForm from '../form';

import {
  updateAgreement,
  createAgreement,
  newAgreement,
} from 'app/actions/tele/agreement';

class TeleAgreementsEdit extends React.Component {
  static propTypes = {
    actions: PropTypes.object,
    agreement: PropTypes.object.isRequired,
    navision: PropTypes.object.isRequired,
  }

  static storeProps = {
    agreement: v => v.teleAgreement,
    navision: v => v.navision,
  }

  static storeActions = {
    newAgreement,
    updateAgreement,
    createAgreement,
  }

  componentDidMount() {
    this.props.actions.newAgreement();
  }

  handleOnChange = (e, partial: ?Object) => {
    const fields = partial || { [e.target.name]: e.target.value };
    this.props.actions.updateAgreement(fields);
  }

  handleOnReset = () => {
    this.componentDidMount();
  }

  handleOnSubmit = () => {
    const payload = fromJS(this.props.agreement).toJS();
    this.props.actions.createAgreement(payload);
  }

  render() {
    const { agreement, navision } = this.props;

    return (
      <div className="agreement-edit">
        <PageHeader
          title="New Agreement"
        />
        <AgreementForm
          customers={navision.customers.toJS()}
          products={navision.products.toJS()}
          agreement={agreement}
          onChange={this.handleOnChange}
          onReset={this.handleOnReset}
          onSubmit={this.handleOnSubmit}
        />
      </div>
    );
  }
}

export default compose(
  TeleAgreementsEdit,
  isConnectable,
);



