import React, { PropTypes } from 'react';
import map from 'lodash/map';
import head from 'lodash/head';
import pick from 'lodash/pick';
import filter from 'lodash/filter';
import compose from 'app/lib/compose';
import FormGroup from 'ui/oneui/form/group';

/**
 * ProductForm component
 * @extend {ReactComponent}
 */
class ProductForm extends React.Component {

  static propTypes = {
    value: PropTypes.any,
    values: PropTypes.object,
    products: PropTypes.array.isRequired,
    onSubmit: PropTypes.func.isRequired,
    onDismiss: PropTypes.func.isRequired,
  }

  state = {
    index: -1,
    description: '',
    navProductNumber: '',
    unitAmount: 0,
    unitPrice: 0,
    navPrice: 0,
    unitType: '',
  }

  componentWillMount() {
    if (this.props.values) {
      this.setState({ ...this.props.values });
    }
  }

  handleInputChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }

  handleBaseProductChanged = (e) => {
    const product = head(filter(this.props.products, { number: parseInt(e.target.value, 10) }));

    const value = pick(product, ['unitType', 'unitPrice']);
    value.navProductNumber = product.number;
    value.description = product.name;
    value.navPrice = product.unitPrice;

    this.setState({ ...value });
  }

  handleSubmit = () => {
    if (!this.state.navProductNumber) {
      this.setState({ alert: 'You need to pick a base product' });
      return;
    }

    if (!this.state.description) {
      this.setState({ alert: 'You are missing a description' });
      return;
    }

    if (!this.state.unitType) {
      this.setState({ alert: 'You are missing a unit type' });
      return;
    }

    this.props.onSubmit(this.state);
  }

  handleEnterSubmit = (e) => {
    if (e.charCode === 13) {
      this.props.onSubmit(this.state);
    }
  }

  render() {
    const backDrop = {
      display: 'block',
      background: 'rgba(0,0,0,0.5)',
    };

    let alert;
    if (this.state.alert) {
      alert = (
        <div className="alert alert-danger" style={{ margin: '-20px -20px 20px' }}>
          <p>{this.state.alert}</p>
        </div>
      );
    }

    return (
      <div className="productform">
        <div className="modal fade in" style={backDrop}>
          <div className="modal-dialog modal-dialog-popin">
            <div className="modal-content">
              <div className="block block-themed block-transparent remove-margin-b">
                <div className="block-header bg-primary-dark">
                  <h3 className="block-title">Add Product</h3>
                </div>
                <div className="block-content form-horizontal">
                  {alert}
                  <FormGroup>
                    <select
                      onChange={this.handleBaseProductChanged}
                      className="form-control"
                      value={this.state.navProductNumber}
                    >
                      <option key="default">pick one ...</option>
                      { map(this.props.products, c =>
                          <option key={c.number} value={c.number}>{c.name} ({c.number})</option>
                        ) }
                    </select>
                    <label htmlFor="product-id">Base Product</label>
                  </FormGroup>
                  <FormGroup>
                    <input
                      name="description"
                      onChange={this.handleInputChange}
                      value={this.state.description}
                      type="text"
                      onKeyPress={this.handleEnterSubmit}
                      className="form-control"
                    />
                    <label>Description</label>
                  </FormGroup>
                  <FormGroup>
                    <input
                      name="unitAmount"
                      onChange={this.handleInputChange}
                      value={this.state.unitAmount}
                      type="text"
                      onKeyPress={this.handleEnterSubmit}
                      className="form-control"
                    />
                    <label>Unit Amount</label>
                  </FormGroup>
                  <FormGroup>
                    <input
                      name="unitPrice"
                      onChange={this.handleInputChange}
                      value={this.state.unitPrice}
                      type="text"
                      onKeyPress={this.handleEnterSubmit}
                      className="form-control"
                    />
                    <label>Unit Price</label>
                  </FormGroup>
                  <FormGroup>
                    <input
                      name="navPrice"
                      value={this.state.navPrice}
                      type="text"
                      readOnly
                      className="form-control"
                    />
                    <label>Unit Retail Price</label>
                  </FormGroup>
                  <FormGroup>
                    <input
                      name="unitType"
                      onChange={this.handleInputChange}
                      value={this.state.unitType}
                      type="text"
                      onKeyPress={this.handleEnterSubmit}
                      className="form-control"
                    />
                    <label>Unit Type</label>
                  </FormGroup>
                </div>
                <div className="modal-footer">
                  <button
                    onClick={this.props.onDismiss}
                    className="btn btn-sm btn-default"
                    data-dismiss="modal"
                  >
                    Cancel
                  </button>
                  <button
                    className="btn btn-sm btn-primary"
                    type="button"
                    onClick={this.handleSubmit}
                  >
                    Save
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default compose(
  ProductForm
);



