import React, { PropTypes } from 'react';
import map from 'lodash/map';
import find from 'lodash/find';
import assign from 'lodash/assign';
import sortBy from 'lodash/sortBy';

import Modal from 'ui/oneui/modal';
import Grid from 'ui/oneui/grid';
import Input from 'ui/oneui/form/input';
import Select from 'ui/oneui/form/select';

import { CountryCodes, ProductTypes, DestinationTypes } from 'app/constants/tele';

export default class ProductForm extends React.Component {
  static propTypes = {
    product: PropTypes.object.isRequired,
    onSave: PropTypes.func,
    onDismiss: PropTypes.func,
    products: PropTypes.array.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = assign({
      description: '',
      productType: 1,
      destinationType: 1,
      prefix: '*',
      navProductNumber: '',
      navProductPrice: 0,
      unitType: '',
      unitPrice: 0,
    }, props.product);
  }

  handleOnChange = (e) => {
    const partial = { [e.target.name]: e.target.value };

    if (e.target.name === 'navProductNumber') {
      const product = find(this.props.products, { number: e.target.value });
      partial.navProductPrice = product.unitPrice;
      partial.unitType = product.unitType;
    }

    this.setState(partial);
  }

  handleOnSave = (e) => {
    const values = this.state;
    values.productType = parseInt(values.productType, 10);
    values.destinationType = parseInt(values.destinationType, 10);
    values.unitPrice = parseFloat(values.unitPrice.toString().replace(',', '.'));

    this.props.onSave(e, values);
  }

  render() {
    const saveButton = (
      <button
        key="save"
        className="btn btn-success"
        onClick={this.handleOnSave}
      >
        Save Changes
      </button>
    );

    const cancelButtons = (
      <button
        key="cancel"
        className="btn btn-default"
        onClick={this.props.onDismiss}
      >
        Cancel
      </button>
    );

    const products = sortBy(map(this.props.products, p => {
      return { text: `${p.number}: ${p.name}`, value: parseInt(p.number, 10) };
    }), 'value');

    const countryCodes = map(CountryCodes, (code, country) => {
      return { text: `${country} (${code})`, value: code };
    });

    const destTypes = map(DestinationTypes, (val, key) => {
      return { text: val, value: key }
    });

    const prodTypes = map(ProductTypes, (val, key) => {
      return { text: val, value: key }
    });

    let price = this.state.unitPrice;
    if (price) {
      price = price.toString().replace(/\./g, ',');
    }

    return (
      <Modal
        title="Edit Product"
        buttons={[cancelButtons, saveButton]}
      >
        <div className="form-horizontal">
          <Grid>
            <Select
              column={9}
              name="navProductNumber"
              label="NAV Product"
              mapping={{ text: 'text', value: 'value' }}
              options={products}
              prompt="Choose a base product ..."
              value={this.state.navProductNumber}
              onChange={this.handleOnChange}
            />
            <Input
              column={3}
              name="navProductPrice"
              label="NAV Price"
              readOnly
              value={this.state.navProductPrice}
              onChange={() => {}}
              className="text-right"
            />
          </Grid>

          <Input
            name="description"
            label="Description"
            value={this.state.description}
            onChange={this.handleOnChange}
          />

          <Grid columns={2}>
            <Select
              name="destinationType"
              label="Destination Type"
              options={destTypes}
              value={this.state.destinationType}
              onChange={this.handleOnChange}
            />

            <Select
              name="prefix"
              label="Destination Prefix"
              options={countryCodes}
              value={this.state.prefix}
              onChange={this.handleOnChange}
            />
          </Grid>

          <Grid columns={3}>
            <Select
              name="productType"
              label="Product Type"
              value={this.state.productType}
              options={prodTypes}
              onChange={this.handleOnChange}
            />
            <Input
              name="unitType"
              label="Unit Type"
              value={this.state.unitType}
              onChange={this.handleOnChange}
              className="text-center"
            />
            <Input
              name="unitPrice"
              label="Unit Price"
              value={price}
              onChange={this.handleOnChange}
              className="text-right"
            />
          </Grid>
        </div>
      </Modal>
    );
  }
}



