import React from 'react';
import map from 'lodash/map';
import reduce from 'lodash/reduce';
import sortBy from 'lodash/sortBy';
import clone from 'lodash/clone';
import ProductForm from './product-form';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import accounting from 'accounting';
import {
  addSubscriptionLine,
  updateSubscriptionLine,
  removeSubscriptionLine,
} from 'app/actions/hosting/subscription';

import Product from './product';

class SubscriptionProductsDetails extends React.Component {
  static storeActions = {
    addSubscriptionLine,
    updateSubscriptionLine,
    removeSubscriptionLine,
  }

  static storeProps = {
    navision: v => ({
      products: v.navision.products.toJS(),
    }),
  }

  static propTypes = {
    products: React.PropTypes.array,
    subscription: React.PropTypes.object,
    navision: React.PropTypes.object,
    actions: React.PropTypes.object,
  }

  state = {
    showProductModal: false,
    product: null,
  }

  handleEditProduct = (index) => {
    const product = clone(this.props.products[index]);
    product.index = index;

    this.setState({
      showProductModal: true,
      product,
      index,
    });
  }

  handleRemoveProduct = (index) => {
    this.props.actions.removeSubscriptionLine(index);
  }

  handleAddProduct = () => {
    const product = {};
    if (this.props.subscription.id) {
      product.billingSubscriptionId = this.props.subscription.id;
    }

    this.setState({
      showProductModal: true,
      product,
    });
  }

  handleModalSubmit = (product) => {
    if (product.index > -1) {
      this.props.actions.updateSubscriptionLine(product);
    } else {
      this.props.actions.addSubscriptionLine(product);
    }

    this.setState({
      showProductModal: false,
      product: null,
      index: -1,
    });
  }

  handleModalDismiss = () => {
    this.setState({
      showProductModal: false,
      product: null,
    });
  }

  render() {
    let productModal;
    if (this.state.showProductModal) {
      const products = map(this.props.navision.products, v => {
        const value = v;
        value.number = parseInt(value.number, 10);

        return value;
      });

      productModal = (
        <ProductForm
          onSubmit={this.handleModalSubmit}
          onDismiss={this.handleModalDismiss}
          values={this.state.product}
          products={sortBy(products, 'number')}
        />
      );
    }

    const subtotal = reduce(
      map(this.props.products, v => v.unitPrice * v.unitAmount),
      (a, b) => a + b
    );

    return (
      <div className="block">
        {productModal}
        <div className="block-header bg-gray-lighter">
          <div className="block-options-simple">
            <button
              onClick={this.handleAddProduct}
              className="btn btn-xs btn-primary"
              type="submit"
            >
              Add Product
            </button>
          </div>
          <div className="block-title">Products</div>
        </div>
        <div className="block-content block-content-full">
          <table className="table table-products table-borderless table-striped table-vcenter">
            <thead>
              <tr>
                <th>Product Name</th>
                <th className="text-right">Unit Amount</th>
                <th>Unit Type</th>
                <th className="text-right">Nav Price</th>
                <th className="text-right">Unit Price</th>
                <th className="text-right">Subtotal</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              { map(this.props.products, (p, i) =>
                  <Product
                    index={i}
                    product={p}
                    key={i}
                    onEdit={this.handleEditProduct.bind(null, i)}
                    onDelete={this.handleRemoveProduct.bind(null, i)}
                  />
                ) }
            </tbody>
            <tfoot>
              <tr>
                <td style={{ borderTop: '1px solid #ddd' }} colSpan={4}>&nbsp;</td>
                <td style={{ borderTop: '1px solid #ddd' }} className="text-right">
                  <b><small>SUBTOTAL</small></b>
                </td>
                <td style={{ borderTop: '1px solid #ddd' }} className="text-right">
                  <b><small>KR. {accounting.format(subtotal, 2, '.', ',')}</small></b>
                </td>
                <td style={{ borderTop: '1px solid #ddd' }}></td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    );
  }
}

export default compose(
  SubscriptionProductsDetails,
  isConnectable
);



