import React, { PropTypes } from 'react';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import isConnectable from 'app/concerns/connectable';
import moment from 'moment';
import _ from 'lodash';
import map from 'lodash/map';
import Block from 'ui/oneui/block';
import Grid from 'ui/oneui/grid';
import Icon from 'ui/icon';

import Entry from './entry';

/**
 * Subscriptions component
 * @extend {ReactComponent}
 */
class SubscriptionsList extends React.Component {
  static stylesheet = require('./styles.scss');

  static storeProps = {
    subscriptions: v => v.hostingSubscriptions.entries.toJS(),
  }

  static propTypes = {
    subscriptions: PropTypes.array,
  }

  render() {
    const ids = _.map(this.props.subscriptions, _.property('navCustomerId'));
    const numberOfCustomers = _.size(_.uniq(ids));
    const numberOfsubscriptions = _.size(this.props.subscriptions);

    const today = moment().format('MM-YYYY');
    const dueThisMonth = _.size(_.filter(this.props.subscriptions, s =>
     s.nextInvoice.format('MM-YYYY') === today
    ));

    return (
      <div className="subscriptions">
        <div className="content">
          <Grid columns={4} size="sm">
            <Block link="/hosting/subscriptions/new" className="text-center font-w700">
              <div className="text-success"><Icon name="add" size={43} /></div>
              <div
                className="block-content block-content-full
                block-content-mini bg-gray-lighter text-success"
              >
                Add New <span className="hidden-xs hidden-sm hidden-md">Subscription</span>
              </div>
            </Block>

            <Block className="text-center font-w700">
              <div className="h1">{numberOfsubscriptions}</div>
              <div className="block-content block-content-full block-content-mini bg-gray-lighter">
                Subscriptions
              </div>
            </Block>

            <Block className="font-w700 text-center">
              <div className="h1">{dueThisMonth}</div>
              <div className="block-content block-content-full block-content-mini bg-gray-lighter">
                Due This Month
              </div>
            </Block>

            <Block className="text-center font-w700">
              <div className="h1">{numberOfCustomers}</div>
              <div className="block-content block-content-full block-content-mini bg-gray-lighter">
                Customers
              </div>
            </Block>
          </Grid>

          <Block
            header="bg-gray-lighter"
            title={`${this.props.subscriptions.length} Subscriptions`}
          >
            <table className="table table-borderless table-striped table-vcenter">
              <thead>
                <tr>
                  <th>Client</th>
                  <th>Description</th>
                  <th>First Invoice</th>
                  <th>Next Invoice</th>
                  <th><div className="text-center">Billing Cycle</div></th>
                  <th><div className="text-right">Subtotal</div></th>
                </tr>
              </thead>
              <tbody>
                { map(this.props.subscriptions, s =>
                    <Entry
                      key={s.id}
                      subscription={s}
                    />
                  ) }
              </tbody>
            </table>
          </Block>
        </div>
      </div>
    );
  }
}

export default compose(
  SubscriptionsList,
  isStyleable,
  isConnectable
);



