import React from 'react';
import cx from 'classnames';
import map from 'lodash/map';
import Group from './group';

export default function FormSelect(props) {
  const { className, id, label, options, mapping, prompt, ...rest } = props;
  const classes = className || [];

  // Build the option list
  const optionList = map(options, (opt, i) => {
    let { value, text } = opt;

    if (mapping) {
      if (typeof mapping.text === 'function') {
        text = mapping.text(opt);
      } else {
        text = opt[mapping.text];
      }

      if (typeof mapping.value === 'function') {
        value = mapping.value(opt, i);
      } else {
        value = opt[mapping.value];
      }
    }
    else if (typeof opt !== 'object') {
      value = opt;
      text = opt;
    }
    // else if (options.constructor.name === 'Array') {
    //   value = opt;
    //   text = opt;
    // }

    return (
      <option
        key={`form-select-option-${i}`}
        value={value}
      >
        {text}
      </option>
    );
  });

  // Add the prompt if given
  if (prompt) {
    optionList.splice(0, 0,
      <option key="form-select-prompt" value="">{prompt}</option>
    );
  }

  let content = (
    <div>
      <label htmlFor={props.id}>{props.label}</label>
      <select
        {...rest}
        id={props.id}
        value={props.value}
        className="form-control"
      >
        {optionList}
      </select>
    </div>
  );

  if (props.wrap) {
    content = <Group>{content}</Group>;
  }

  return content;
}

FormSelect.propTypes = {
  className: React.PropTypes.string,
  id: React.PropTypes.string,
  label: React.PropTypes.string,
  mapping: React.PropTypes.object,
  options: React.PropTypes.array,
  prompt: React.PropTypes.string,
  value: React.PropTypes.any,
  wrap: React.PropTypes.bool,
};

FormSelect.defaultProps = {
  id: '',
  label: '',
  options: [],
  wrap: true,
};



