import k from 'app/constants';
import { errorHandler, fetchJsonOnSuccess } from 'app/lib/fetch-utils';
import axios from 'axios';

/**
 * Hydrate the agreements
 * @return {Function}
 */
export function hydrateAgreements(): Function {
  return (dispatch) => {
    dispatch({ type: k.TELE_AGREEMENTS_HYDRATE });

    axios.get(`${k.API_HOST}/api/teleAgreements`)
      .then(response => {
        const agreements = response.data;
        dispatch({ type: k.TELE_AGREEMENTS_HYDRATED, agreements });
      })
      .catch(errorHandler);
  };
}



