import React, { PropTypes } from 'react';
import k from 'app/constants';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import { Link } from 'react-router';
import cx from 'classnames';

/**
 * Block component
 * @extend {ReactComponent}
 */
class Block extends React.Component {
  static stylesheet = require('./block.scss');

  static propTypes = {
    link: PropTypes.string,
    className: PropTypes.any,
    title: PropTypes.string,
    titleClassName: PropTypes.any,
    themed: PropTypes.bool,
    onClick: PropTypes.func,
    children: PropTypes.any,
  }

  static defaultProps = {
    titleClassName: 'bg-gray-lighter',
  }

  render() {
    const { link, className, title, titleClassName, themed, onClick, ...rest } = this.props;

    // There can be a few different variations of the block, so it
    // needs to be flexible.
    let children = this.props.children || [];

    // Build all the different version of the block container classes
    const containerClasses = cx(['block', className, {
      'block-themed': themed,
      'block-link-hover3': link || onClick,
    }]);

    // Any child that isn't inside a block content element, will be
    // wrapped in order to ensure persistent look.
    children = React.Children.map(children, (child, key) => {
      if (child === null) {
        return child;
      }

      if (!child.props || !child.props.className || child.props.className.indexOf('block-content') === -1) {
        // If the child is a p tag, it will contain it's own margin,
        // else we added the full content class
        const classes = cx('block-content', {
          'block-content-full': child.type !== 'p',
        });

        return (
          <div key={key} className={classes}>
            {child}
          </div>
        );
      }

      return React.cloneElement(child, { key });
    });

    // If the block has a title
    if (title) {
      children.splice(0, 0, (
        <div key="header" className={`block-header ${titleClassName}`}>
          <div className="block-title">
            {title}
          </div>
        </div>
      ));
    }

    // If the link property has been set, an actual link gets returned
    if (link) {
      const url = `${k.BASEURL}${link}`.replace(/\/\//, '/');

      return (
        <Link to={url} className={containerClasses}>
          {children}
        </Link>
      );
    } else if (onClick) {
      return (
        <a style={{ cursor: 'pointer' }} onClick={onClick} className={containerClasses}>
          {children}
        </a>
      );
    }

    return (
      <div className={containerClasses}>
        {children}
      </div>
    );
  }
}

export default compose(
  Block,
  isStyleable
);



