import k from 'app/constants';
import pick from 'lodash/pick';
import each from 'lodash/each';
import axios from 'axios';
import moment from 'moment';

function sanitize(subscription) {
  const entry = pick(subscription, [
    'id',
    'description',
    'products',
    'billingCycle',
    'auditHistory',
    'nextInvoice',
    'nextPeriod',
    'firstInvoice',
    'paymentTerms',
    'navCustomerId',
    'navCustomerName',
    'additionalText',
  ]);

  each(entry, (val, key) => {
    if (typeof val === 'string') {
      entry[key] = val.trim();
    }
  });

  if (entry.firstInvoice) {
    entry.firstInvoice = moment(subscription.firstInvoice.substring(0, 19));
  } else {
    entry.firstInvoice = moment();
  }

  if (entry.nextInvoice) {
    entry.nextInvoice = moment(subscription.nextInvoice.substring(0, 19));
  } else {
    entry.nextInvoice = moment();
  }

  if (process.env.NODE_ENV === 'development' && !entry.id) {
    entry.id = (new Date).getTime();
  }

  return entry;
}

function errorHandler(error) {
  if (error && error.statusText) {
    alert(`${error.status}: ${error.statusText}\n${error.data}`);
  } else {
    console.error('captured unkown error:', error);
  }
}

export function createSubscription(value) {
  return (dispatch) => {
    dispatch({ type: k.HOSTING_SUBSCRIPTION_CREATE });

    // Normalize the data for sending
    const payload = value.fields;
    payload.products = value.products;

    axios.post(`${k.API_HOST}/api/subscriptions`, payload)
      .then(response => {
        dispatch({
          type: k.HOSTING_SUBSCRIPTION_CREATE_SUCCESS,
          subscription: sanitize(response.data),
        });
      })
      .catch(errorHandler);
  };
}

export function persistSubscription(id, subscription) {
  return (dispatch) => {
    dispatch({ type: k.HOSTING_SUBSCRIPTION_PERSIST });

    axios.put(`${k.API_HOST}/api/subscriptions/${id}`, subscription)
      .then(response => {
        dispatch({
          type: k.HOSTING_SUBSCRIPTION_PERSIST_SUCCESS,
          subscription: sanitize(response.data),
        });
      })
      .catch(errorHandler);
  };
}

export function cancelSubscription(id) {
  return (dispatch) => {
    dispatch({ type: k.HOSTING_SUBSCRIPTION_CANCEL });

    axios.delete(`${k.API_HOST}/api/subscriptions/${id}`).
      then(() => {
        dispatch({
          type: k.HOSTING_SUBSCRIPTION_CANCEL_SUCCESS,
          id,
        });
      })
      .catch(errorHandler);
  };
}

export function editSubscription(subscription: Object) {
  return {
    type: k.HOSTING_SUBSCRIPTION_EDIT,
    subscription,
  };
}

export function updateSubscription(fields: Object) {
  return {
    type: k.HOSTING_SUBSCRIPTION_UPDATE,
    fields,
  };
}

export function resetSubscription() {
  return {
    type: k.HOSTING_SUBSCRIPTION_RESET,
  };
}

export function addSubscriptionLine(value: Object) {
  return {
    type: k.HOSTING_SUBSCRIPTION_LINES_ADD,
    value,
  };
}

export function updateSubscriptionLine(product: Object) {
  return {
    type: k.HOSTING_SUBSCRIPTION_LINES_UPDATE,
    product,
  };
}

export function removeSubscriptionLine(index: number) {
  return {
    type: k.HOSTING_SUBSCRIPTION_LINES_REMOVE,
    index,
  };
}

/**
 * Send out a single subscription to NAV
 * @param  {Number} id
 * @param  {Moment} date
 * @return {Promise}
 */
export function billSubscription(id: number, date: Object) {
  return axios.post(`${k.API_HOST}/api/subscriptions/${id}/bill/${date.format('YYYY-MM-DD')}`)
      .then(resp => {
        if (resp.status !== 200) {
          return Promise.rejected(resp.data);
        }

        return Promise.resolve(resp.data);
      });
}



