import React, { PropTypes } from 'react';
import sortBy from 'lodash/sortBy';

import Block from 'ui/oneui/block';
import FormSelect from 'ui/oneui/form/select';
import FormInput from 'ui/oneui/form/input';

const AgreementFormDetails = ({
  description,
  navCustomerId,
  customers,
  onChange,
  onCustomerChange,
}) => (
  <Block title="Details">
    <div className="block-content">
      <FormSelect
        name="customer"
        label="Customer"
        prompt="..."
        value={navCustomerId}
        mapping={{ value: 'number', text: 'name' }}
        options={sortBy(customers, 'name')}
        onChange={onCustomerChange}
      />
      <FormInput
        name="description"
        label="Description"
        value={description}
        onChange={onChange}
      />
    </div>
  </Block>
);

AgreementFormDetails.propTypes = {
  navCustomerId: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  customers: PropTypes.array.isRequired,
  onChange: PropTypes.func.isRequired,
  onCustomerChange: PropTypes.func.isRequired,
};

export default AgreementFormDetails;



