import k from 'app/constants';
import _ from 'lodash';
import axios from 'axios';
import moment from 'moment';

function normalize(subscription) {
  const entry = _.pick(subscription, [
    'id',
    'description',
    'products',
    'billingCycle',
    'auditHistory',
    'firstInvoice',
    'nextInvoice',
    'nextPeriod',
    'paymentTerms',
    'navCustomerId',
    'navCustomerName',
    'additionalText',
  ]);

  _.each(entry, (val, key) => {
    if (typeof val === 'string') {
      entry[key] = val.trim();
    }
  });

  entry.firstInvoice = moment(entry.firstInvoice);
  entry.nextInvoice = moment(entry.nextInvoice);

  return entry;
}

/**
 * @param  {Object} payload
 * @return {Object}
 */
export function hydrateSubscriptions() {
  return (dispatch) => {
    dispatch({ type: k.HOSTING_SUBSCRIPTIONS_HYDRATE });

    axios.get(`${k.API_HOST}/api/subscriptions`).then(response => {
      const subscriptions = _.map(response.data, normalize);

      dispatch({
        type: k.HOSTING_SUBSCRIPTIONS_HYDRATED,
        subscriptions,
      });
    });
  };
}



