import each from 'lodash/each';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

/**
 * Maps properties from the store state, into the component state
 * @param  {Object} state
 * @return {Object}
 */
function mapStateToProps(state: Object) {
  const props = this.storeProps;

  // If the properties aren't  an object, we return empty
  if (typeof props !== 'object') {
    return {};
  }

  // Map the values from the state store
  const values = {};
  each(props, (fn, key) => {
    if (typeof fn === 'function') {
      values[key] = fn(state);
    } else {
      values[key] = state[key];
    }
  });

  return values;
}

/**
 * Uses the storeAction property to generate a dispatch collection
 * @param  {Function} dispatch
 * @return {Object}
 */
function mapDispatchToProps(dispatch: Function) {
  const creators = this.storeActions || {};
  const actions = bindActionCreators(creators, dispatch);
  return { actions };
}

/**
 * Uses the mapStateToProps and mapDispatchToProps methods from the
 * component, if they have been defined. Then it wraps the component
 * in the connector.
 * @param  {ReactComponent} Component
 * @return {ReactComponent}
 */
function isConnectable(Component) {
  const fn = connect(
    mapStateToProps.bind(Component),
    mapDispatchToProps.bind(Component)
  );

  return fn(Component);
}

/**
 * Marks that the composer will wrap the component
 * @type {Boolean}
 */
isConnectable.willWrapComponent = true;

// Exports the composer
export default isConnectable;



