import React, { Component, PropTypes } from 'react';
import moment from 'moment';
import FormInput from 'ui/oneui/form/input';
import DatePicker from 'ui/oneui/date-picker';
import { billSubscription } from 'app/actions/hosting/subscription';

export default class ManualBilling extends Component {
  static propTypes = {
    id: PropTypes.number.isRequired,
    values: PropTypes.arrayOf(PropTypes.string).isRequired,
    selected: PropTypes.string.isRequired,
    onSent: PropTypes.func.isRequired,
    onCancel: PropTypes.func.isRequired,
  }

  state = {
    isSending: false,
    showDatePicker: false,
    period: moment(),
    message: null,
  }

  handleSend = () => {
    this.setState({ isSending: true });

    billSubscription(this.props.id, this.state.period).then(resp => {
      this.setState({ message: resp.message, isSending: false })
      setTimeout(() => this.props.onSent(), 3000);
    });
  }

  handlePeriodChange = (period) => {
    this.setState({ period, showDatePicker: false });
  }

  handleDateFieldOnFocus = () => {
    this.setState({ showDatePicker: true });
  }

  handleDateDismiss = () => {
    this.setState({ showDatePicker: false });
  }

  render() {
    const styles = { padding: '10px 10px 0', border: '1px solid #f3f3f3', margin: '0 auto 20px' };
    if (this.state.isSending) {
      return (
        <div className="bg-gray-lighter text-center" style={{ ...styles, padding: 10 }}>
          Creating invoice draft for {this.state.period.format('MMMM, Do')} ...
        </div>
      );
    }

    if (this.state.message) {
      return (
        <div className="bg-gray-lighter text-center" style={{ ...styles, padding: 10 }}>
          {this.state.message}
        </div>
      );
    }

    let picker = null;
    if (this.state.showDatePicker) {
      picker = (
        <DatePicker
          onDismiss={this.handleDateDismiss}
          onSelect={this.handlePeriodChange}
        />
      );
    }

    return (
      <div className="bg-gray-lighter" style={styles} key="manual-billing">
        {picker}
        <div className="row">
          <div className="col-xs-6">
            <FormInput
              readOnly
              onFocus={this.handleDateFieldOnFocus}
              onChange={() => {}}
              value={this.state.period.format('MMMM, Do')}
              label="Billing period"
            />
          </div>
          <div className="col-xs-6">
            <label htmlFor="">&nbsp;</label>
            <div className="actions">
              <button
                disabled={this.state.period === '...'}
                onClick={this.handleSend}
                className="btn btn-success push-10-r"
              >
                Send
              </button>
              <button
                onClick={() => this.props.onCancel()}
                className="btn btn-warning"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



