const env = {};
if (process.env.NODE_ENV === 'production') {
  env.API_HOST = 'https://nav.gowingu.net:8091/RPBilling';
  env.BASEURL = '/RPBilling/';
} else {
  env.API_HOST = `http://${process.env.API_HOST}/BillingAPI`;
  env.BASEURL = '/';
}

// Export the variable
export default env;



