import _ from 'lodash';
import { toSymbol } from 'app/lib/collection';
import routing from './routing';
import navision from './navision';
import hosting from './hosting';
import env from './env';
import tele from './tele';

// Require all files in the constants folder
const imported = [
  routing,
  navision,
  hosting,
  tele,
  env,
];

// Go through each imported file and merge the partial objects
// into the finale result object.
let result = {};
_.each(imported, (content) => {
  let partial = {};

  // If the imported file is an array, we need to create an object
  // where the values matches the keys.
  if (_.isArray(content)) {
    const keys = _.values(content);
    partial = _.zipObject(keys, keys);
  // If the imported file is an object, we check if there are empty
  // values that needs to be mirrored.
  } else if (_.isObject(content)) {
    _.each(content, (value, key) => {
      partial[key] = value || toSymbol(key);
    });
  }

  // Extend the result object with the newly created partial object
  result = _.extend({}, result, partial);
});

// Export the final result
export default result;



