import k from 'app/constants';
import pick from 'lodash/pick';
import { Map, List } from 'immutable';
import moment from 'moment';
import { nextDateInCycle } from 'app/lib/billing';

/**
 * Define the shape of the reducers state
 * @type {FlowType}
 */
type State = {
  deleting: boolean,
  editing: boolean,
  posting: boolean,
  fields: Map,
  products: List,
};

/**
 * Define the initial/clean state for the reducer
 * @type {FlowType}
 */
const initialState: State = {
  deleting: false,
  posting: false,
  editing: false,
  changed: [],
  fields: Map({
    navCustomerId: '',
    navCustomerName: '',
    description: '',
    billingCycle: 'Monthly',
    firstInvoice: moment(),
    nextInvoice: moment(),
    nextPeriod: moment().add(1, 'month'),
    paymentTerms: 30,
    additionalText: '',
  }),
  products: List(),
};

/**
 * subscription reducer
 * @param  {Object} state
 * @param  {Object} action
 * @return {Object}
 */
export default function reducer(state: State = initialState, action: Object): State {
  switch (action.type) {
    case k.HOSTING_SUBSCRIPTION_CREATE: {
      return { ...state, posting: true };
    }

    case k.HOSTING_SUBSCRIPTION_CREATE_SUCCESS: {
      return { ...initialState, posting: false };
    }

    case k.HOSTING_SUBSCRIPTION_PERSIST: {
      return { ...state, posting: true, changed: [] };
    }

    case k.HOSTING_SUBSCRIPTION_PERSIST_SUCCESS: {
      return { ...state, posting: false };
    }

    case k.HOSTING_SUBSCRIPTION_UPDATE: {
      const fields = state.fields.merge(action.fields);
      return { ...state, fields };
    }

    case k.HOSTING_SUBSCRIPTION_RESET: {
      return initialState;
    }

    case k.HOSTING_SUBSCRIPTION_LINES_ADD: {
      const product = action.value;
      product.index = state.products.size;

      const products = state.products.push(product);
      return { ...state, products };
    }

    case k.HOSTING_SUBSCRIPTION_LINES_UPDATE: {
      const products = state.products.update(
        action.product.index,
        () => action.product
      );

      return { ...state, products };
    }

    case k.HOSTING_SUBSCRIPTION_LINES_REMOVE: {
      const products = state.products.remove(action.index);
      return { ...state, products };
    }

    case k.HOSTING_SUBSCRIPTION_EDIT: {
      const fields = Map(pick(action.subscription, [
        'id',
        'billingCycle',
        'description',
        'firstInvoice',
        'nextInvoice',
        'paymentTerms',
        'navCustomerId',
        'navCustomerName',
        'additionalText',
      ]));

      const products = List(action.subscription.products);
      return { ...state, fields, products, editing: true };
    }

    case k.HOSTING_SUBSCRIPTION_CANCEL: {
      return { ...state, deleting: true };
    }

    case k.HOSTING_SUBSCRIPTION_CANCEL_SUCCESS: {
      return { ...state, deleting: false };
    }

    default: {
      return state;
    }
  }
}




