import React, { PropTypes } from 'react';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';

const SimpleTitle = ({ title, children }) => (
  <div className="content bg-gray-lighter">
    <div className="row items-push">
      <div className="col-sm-6">
        <h1 className="page-heading">{title}</h1>
      </div>
      <div className="col-sm-6 text-right">
        {children}
      </div>
    </div>
  </div>
);

SimpleTitle.propTypes = {
  children: PropTypes.any,
  title: PropTypes.string.isRequired,
};

const TitleWithSubtitle = ({ title, subtitle }) => (
  <div className="content bg-image bg-default overflow-hidden">
    <div className="push-50-t push-15">
      <h1 className="h2 text-white animated fadeIn">{title}</h1>
      <h2 className="h5 text-white-op animated fadeIn">{subtitle}</h2>
    </div>
  </div>
);

TitleWithSubtitle.propTypes = {
  subtitle: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

/**
 * PageHeader component
 * @extend {ReactComponent}
 */
class PageHeader extends React.Component {
  static stylesheet = require('./page-header.scss');

  static propTypes = {
    title: React.PropTypes.string.isRequired,
    subtitle: React.PropTypes.string,
  }

  render() {
    if (this.props.subtitle) {
      return <TitleWithSubtitle {...this.props} />;
    }

    return <SimpleTitle {...this.props} />;
  }
}

export default compose(
  PageHeader,
  isStyleable
);



