import React, { PropTypes } from 'react';
import { hydrateAgreements } from 'app/actions/tele/agreements';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import Loading from 'ui/loading';

class Hosting extends React.Component {
  static storeProps = {
    hydrated: v => v.teleAgreements.hydrated,
  }

  static storeActions = {
    hydrateAgreements,
  }

  static propTypes = {
    actions: PropTypes.object,
    children: PropTypes.any,
    hydrated: PropTypes.bool,
  }

  componentDidMount() {
    if (!this.props.hydrated) {
      this.props.actions.hydrateAgreements();
    }
  }

  render() {
    if (!this.props.hydrated) {
      return <Loading noFadeIn />;
    }

    return (
      <div>
        {this.props.children}
      </div>
    );
  }
}

export default compose(
  Hosting,
  isConnectable
);



