import React, { PropTypes } from 'react';
import Icon from 'ui/icon';
import Block from 'ui/oneui/block';
import Grid from 'ui/oneui/grid';

class AgreementActions extends React.Component {
  static propTypes = {
    id: PropTypes.number.isRequired,
    onCancel: PropTypes.func.isRequired,
    isDeleting: PropTypes.bool,
  }

  handleCancelAgreement = () => {
    if (confirm('Are you sure you wan\'t to cancel this agreement ?')) {
      this.props.onCancel();
    }
  }

  render() {
    return (
      <Grid columns={2}>
        <Block
          link={`/tele/agreements/${this.props.id}/edit`}
          className="text-center font-w700"
        >
          <div className="text-success"><Icon name="edit" size={43} /></div>
          <div
            className="block-content block-content-full block-content-mini
            bg-gray-lighter text-success"
          >
            Edit <span className="hidden-xs hidden-sm hidden-md">Agreement</span>
          </div>
        </Block>
        <Block onClick={this.handleCancelAgreement} className="text-center font-w700">
          <div className="text-danger"><Icon name="delete" size={43} /></div>
          <div
            className="block-content block-content-full
            block-content-mini bg-gray-lighter text-danger"
          >
            Cancel <span className="hidden-xs hidden-sm hidden-md">Agreement</span>
          </div>
        </Block>
      </Grid>
    );
  }
}

export default AgreementActions;



