import accounting from 'accounting';

/**
 * Returns the value formatted according to danish rules
 * @param  {Number} value
 * @return {String}
 */
export function dkk(value: number) : string {
  return accounting.formatMoney(value, 'kr. ', 2, '.', ',');
}



