/**
 * Handles request errors
 * @param  {Object} response
 */
export function errorHandler(response: Object) {
  console.error(response);
}

/**
 * Returns the json promise, or throws an error
 * @param  {Object} response
 * @return {Promise}
 */
export function fetchJsonOnSuccess(response: Object) {
  if (response.status !== 200) {
    return Promise.failed(response.statusText);
  }

  return response.json();
}



