import React from 'react';
import accounting from 'accounting';

/**
 * Product component
 * @extend {ReactComponent}
 */
class Product extends React.Component {
  static propTypes = {
    id: React.PropTypes.number,
    description: React.PropTypes.string.isRequired,
    unitAmount: React.PropTypes.number.isRequired,
    unitType: React.PropTypes.string.isRequired,
    unitPrice: React.PropTypes.number.isRequired,
    navPrice: React.PropTypes.number,
  }

  render() {
    const {
      id,
      description,
      unitAmount,
      unitType,
      unitPrice,
      navPrice,
    } = this.props;

    return (
      <tr>
        <td>
          <div className="text-center">{id}</div>
        </td>
        <td><b>{description}</b></td>
        <td>
          <div className="text-right">{unitAmount}</div>
        </td>
        <td>{unitType}</td>
        <td>
          <div className="text-right">
            kr. {accounting.format(navPrice, 2, '.', ',')}
          </div>
        </td>
        <td>
          <div className="text-right">
            kr. {accounting.format(unitPrice, 2, '.', ',')}
          </div>
        </td>
        <td>
          <div className="text-right">
            kr. {accounting.format(unitPrice * unitAmount, 2, '.', ',')}
          </div>
        </td>
      </tr>
    );
  }
}

export default Product;



