import React, { PropTypes } from 'react';
import head from 'lodash/head';
import filter from 'lodash/filter';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import SubscriptionForm from '../form';
import PageHeader from 'ui/oneui/page-header';
import {
  editSubscription,
  persistSubscription,
} from 'app/actions/hosting/subscription';

class SubscriptionsNew extends React.Component {
  static storeActions = {
    editSubscription,
    persistSubscription,
  }

  static storeProps = {
    posting: v => v.hostingSubscription.posting,
    subscriptions: v => v.hostingSubscriptions.entries.toJS(),
    fields: v => v.hostingSubscription.fields.toJS(),
    products: v => v.hostingSubscription.products.toJS(),
  }

  static propTypes = {
    actions: PropTypes.object,
    fields: PropTypes.object,
    params: PropTypes.object,
    posting: PropTypes.bool,
    products: PropTypes.array.isRequired,
    subscriptions: PropTypes.array.isRequired,
  }

  componentDidMount() {
    const id = parseInt(this.props.params.id, 10);
    const subscription = head(filter(this.props.subscriptions, { id }));
    this.props.actions.editSubscription(subscription);
  }

  handleOnSubmit = () => {
    const fields = this.props.fields;

    if (!fields.navCustomerId) {
      alert('Please select a customer');
      return;
    }

    if (!fields.description) {
      alert('A subscription requires a description');
      return;
    }

    const subscription = fields;
    subscription.products = this.props.products;

    this.props.actions.persistSubscription(
      this.props.params.id,
      subscription
    );
  }

  handleOnReset = () => {
    this.componentDidMount();
  }

  render() {
    return (
      <div className="subscription">
        <PageHeader
          title="Edit Subscription"
        />
        <SubscriptionForm
          fields={this.props.fields}
          products={this.props.products}
          onSubmit={this.handleOnSubmit}
          onReset={this.handleOnReset}
        />
      </div>
    );
  }
}

export default compose(
  SubscriptionsNew,
  isConnectable
);



