import React, { PropTypes } from 'react';
import moment from 'moment';
import recur from 'moment-recur';
import head from 'lodash/head';
import filter from 'lodash/filter';
import map from 'lodash/map';
import Block from 'ui/oneui/block';
import FormSelect from 'ui/oneui/form/select';
import FormInput from 'ui/oneui/form/input';
import FormGroup from 'ui/oneui/form/group';
import DatePicker from 'ui/oneui/date-picker';
import Icon from 'ui/icon';
import { nextDateInCycle } from 'app/lib/billing';
import ManualBilling from './billing-manually';

export default class SubscriptionBillingDetails extends React.Component {
  static propTypes = {
    onChange: PropTypes.func,
    subscription: PropTypes.object,
  }

  state = {
    showDatePicker: false,
    showManualBilling: false,
  }

  handleDateFieldOnFocus = () => {
    this.setState({ showDatePicker: true });
  }

  handleDateDismiss = () => {
    this.setState({ showDatePicker: false });
  }

  handleDateSelect = (firstInvoice) => {
    const { paymentTerms, billingCycle } = this.props.subscription;
    const params = this.getNextInvoiceCycleDate(firstInvoice, billingCycle, paymentTerms);
    params.firstInvoice = firstInvoice;

    this.props.onChange(null, params);
    this.setState({ showDatePicker: false });
  }

  handlePaymentTerms = (e) => {
    const { firstInvoice, billingCycle } = this.props.subscription;
    const paymentTerms = e.target.value;

    const params = this.getNextInvoiceCycleDate(firstInvoice, billingCycle, paymentTerms);
    params.paymentTerms = paymentTerms;

    this.props.onChange(null, params);
  }

  handleCycleChange = (e) => {
    const { firstInvoice, paymentTerms } = this.props.subscription;
    const billingCycle = e.target.value;

    const params = this.getNextInvoiceCycleDate(firstInvoice, billingCycle, paymentTerms);
    params.billingCycle = billingCycle;

    this.props.onChange(null, params);
  }

  getMonthsInCycle = (billingCycle) => {
    switch (billingCycle) {
      case 'Quaterly': return 3;
      case 'Biannually': return 6;
      case 'Annually': return 12;
      default: return 1;
    }
  }

  getNextInvoiceCycleDate = (firstInvoice, billingCycle, offset) => {
    const today = moment();
    const months = this.getMonthsInCycle(billingCycle);

    // Calculate the next period
    let nextPeriod = moment(firstInvoice);
    let nextInvoice = moment(firstInvoice).add(-(Math.abs(offset)), 'days');

    // Move the period until the next invoice is later than today
    while (nextInvoice < today) {
      nextPeriod = moment(nextPeriod).add(months, 'months');
      nextInvoice = moment(nextPeriod).add(-(Math.abs(offset)), 'days');
    }

    return { nextPeriod, nextInvoice };
  }

  getSummaryText = () => {
    const { billingCycle, firstInvoice, paymentTerms } = this.props.subscription;
    const {
      nextPeriod,
      nextInvoice,
    } = this.getNextInvoiceCycleDate(firstInvoice, billingCycle, paymentTerms);

    const nextInvoicePayable = nextPeriod.format('YYYY-MM-DD');
    let nextInvoiceSent = nextInvoice.format('YYYY-MM-DD');

    return `Next period is ${nextInvoicePayable}, and will be sent out ${nextInvoiceSent}`;
  }

  render() {
    const subscription = this.props.subscription;
    const { billingCycle, firstInvoice, paymentTerms } = subscription;

    let picker = null;
    if (this.state.showDatePicker) {
      picker = (
        <DatePicker
          onDismiss={this.handleDateDismiss}
          onSelect={this.handleDateSelect}
          value={subscription[this.state.dateField]}
        />
      );
    }

    let summary = null;
    if (firstInvoice && billingCycle && paymentTerms) {
      summary = <p className="text-center"><code>{this.getSummaryText()}</code></p>;
    }

    let manualBilling;
    if (this.state.showManualBilling) {
      manualBilling = (
        <ManualBilling
          id={subscription.id}
          values={moment.months()}
          selected={moment().format('MMMM')}
          onSent={() => this.setState({ showManualBilling: false })}
          onCancel={() => this.setState({ showManualBilling: false })}
        />
      );
    }

    return (
      <Block title="billing">
        {picker}

        <div className="block-content">
          <div key="auto-billing" className="clearfix">
            <div className="row">
              <div className="col-sm-3">
                <FormSelect
                  value={subscription.billingCycle}
                  name="billingCycle"
                  label="Billing Cycle"
                  onChange={this.handleCycleChange}
                  options={['Annually', 'Biannually', 'Quaterly', 'Monthly']}
                />
              </div>
              <div className="col-sm-3">
                <FormInput
                  readOnly
                  onFocus={this.handleDateFieldOnFocus}
                  onChange={() => {}}
                  name="firstInvoice"
                  value={subscription.firstInvoice.format('MMMM, Do')}
                  label="First Periode"
                />
              </div>
              <div className="col-sm-3">
                <FormInput
                  value={subscription.paymentTerms}
                  name="paymentTerms"
                  label="Days Before"
                  onChange={this.handlePaymentTerms}
                />
              </div>
              <div className="col-sm-3">
                <label>&nbsp;</label>
                <div>
                  <button
                    disabled={this.state.showManualBilling}
                    className="btn btn-warning"
                    onClick={() => this.setState({ showManualBilling: true })}
                  >
                    Manual Invoice
                  </button>
                </div>
              </div>
            </div>
            {summary}
          </div>
          {manualBilling}
        </div>
      </Block>
    );
  }
}



