import React, { PropTypes } from 'react';
import SpinKit from 'react-spinkit';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';

/**
 * Loading component
 * @extend {ReactComponent}
 */
class Loading extends React.Component {
  static stylesheet = require('./loading.scss');

  static propTypes = {
    noFadeIn: PropTypes.bool,
  }

  state = {
    tick: 0,
    timer: null,
  }

  componentWillMount() {
    const timer = setTimeout(() => {
      this.setState({ tick: this.state.tick + 1 });
    }, 1000);

    this.setState({ timer });
  }

  componentWillUnmount() {
    const timer = clearTimeout(this.state.timer);
    this.setState({ timer });
  }

  render() {
    let message = 'Please wait ...';

    if (this.state.counter > 7) {
      message = 'The server is a little slow today ...';
    }

    if (this.state.counter > 15) {
      message = 'Welcome to RackPeople Hosting :)';
    }

    if (this.state.counter > 30) {
      message = 'Why are you still waiting, refresh allready!';
    }

    return (
      <div className={['loading', { 'fade-in': !this.props.noFadeIn }]}>
        <div className="container">
          <div className="spinner">
            <SpinKit
              noFadeIn={this.props.noFadeIn} spinnerName="cube-grid"
            />
          </div>
          {message}
        </div>
      </div>
    );
  }
}

export default compose(
  Loading,
  isStyleable
);



