import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import routes from 'app/config/routes';
import store from 'app/stores/store';

if (process.env.NODE_ENV === 'production') {
  require('app/public/index.html');
  require('app/public/manifest.appcache');
}

// Require the oneui main files
require('app/theme/bootstrap.css');
require('app/theme/oneui.css');

// Attempt to get the root node from the body. If it doesn't exists
// we need to create one.
let node = document.getElementById('root');
if (!node) {
  node = document.createElement('div', 'react-mount-node');
  node.id = 'root';
  document.body.appendChild(node);
}

// Create a provider object, with the default store, and the routes
// that has been defined in the config/routes.js file
const app = (
  <Provider store={store}>
    {routes}
  </Provider>
);

// Render the element into the mount node
render(app, node);



