import { List } from 'immutable';
import assign from 'lodash/assign';
import k from 'app/constants';

/**
 * Define the shape of the reducers state
 * @type {FlowType}
 */
type State = {
  fetching: boolean,
  posting: boolean,
  id: number,
};

/**
 * Define the initial/clean state for the reducer
 * @type {FlowType}
 */
const initialState: State = {
  auditHistory: [],
  description: '',
  fetching: false,
  id: 0,
  navCustomerId: '',
  numbers: List(),
  posting: false,
};

function normalize(agreement) {
  const v = assign({}, agreement);

  v.numbers = List(v.numbers.map(n => {
    const number = n;
    number.products = List(number.products);

    return number;
  }));

  return v;
}

/**
 * Agreements reducer
 * @param  {Object} state
 * @param  {Object} action
 * @return {Object}
 */
export default function reducer(state: State = initialState, action: Object): State {
  switch (action.type) {
    case k.TELE_AGREEMENT_LOAD: {
      return { ...state, fetching: true };
    }

    case k.TELE_AGREEMENT_NEW: {
      return initialState;
    }

    case k.TELE_AGREEMENT_LOADED: {
      const agreement = normalize(action.agreement);
      return { ...state, ...agreement, fetching: false };
    }

    case k.TELE_AGREEMENT_CANCEL: {
      return { ...state, posting: true };
    }

    case k.TELE_AGREEMENT_CANCELLED: {
      return { ...state, posting: false, id: 0 };
    }

    case k.TELE_AGREEMENT_UPDATE: {
      return { ...state, ...action.fields };
    }

    case k.TELE_AGREEMENT_PERSIST: {
      return { ...state, posting: true };
    }

    case k.TELE_AGREEMENT_PERSISTED: {
      const agreement = normalize(action.agreement);
      return { ...state, ...agreement, posting: false };
    }

    case k.TELE_AGREEMENT_CREATE: {
      return { ...state, posting: true };
    }

    case k.TELE_AGREEMENT_CREATED: {
      const agreement = normalize(action.agreement);
      return { ...state, ...agreement, posting: false };
    }

    case k.TELE_AGREEMENT_NUMBER_REMOVE: {
      const numberIndex = state.numbers.findIndex(e => e.id === action.numberId);
      const numbers = state.numbers.delete(numberIndex);
      return { ...state, numbers };
    }

    case k.TELE_AGREEMENT_NUMBER_UPDATE: {
      const numbers = state.numbers.set(
        state.numbers.findIndex(e => e.id === action.number.id),
        action.number
      );

      return { ...state, numbers };
    }

    case k.TELE_AGREEMENT_NUMBER_INSERT: {
      const numbers = state.numbers.push(action.number);
      return { ...state, numbers };
    }

    case k.TELE_AGREEMENT_NUMBER_PRODUCT_REMOVE: {
      const numberIndex = state.numbers.findIndex(e => e.id === action.numberId);

      const number = state.numbers.get(numberIndex);
      number.products = number.products.delete(action.productId);

      const numbers = state.numbers.set(numberIndex, number);
      return { ...state, numbers };
    }

    case k.TELE_AGREEMENT_NUMBER_PRODUCT_UPDATE: {
      const numberIdx = state.numbers.findIndex(e => e.id === action.numberId);

      const number = state.numbers.get(numberIdx);
      number.products = number.products.set(action.product.index, action.product);

      const numbers = state.numbers.set(numberIdx, number);
      return { ...state, numbers };
    }

    case k.TELE_AGREEMENT_NUMBER_PRODUCT_INSERT: {
      const numberIndex = state.numbers.findIndex(e => e.id === action.numberId);

      const number = state.numbers.get(numberIndex);
      number.products = number.products.push(action.product);

      const numbers = state.numbers.set(numberIndex, number);
      return { ...state, numbers };
    }

    default: {
      return state;
    }
  }
}



