import React, { PropTypes } from 'react';
import accounting from 'accounting';
import Icon from 'ui/icon';

export default function SubscriptionProduct({ product, onEdit, onDelete }) {
  const { description, unitAmount, unitType, unitPrice, navPrice } = product;
  const subtotal = unitPrice * unitAmount;

  return (
    <tr>
      <td>{description}</td>
        <td>
          <div className="text-right">{unitAmount}</div>
        </td>
        <td>
          {unitType}
        </td>
        <td>
          <div className="text-right">kr. {accounting.format(navPrice, 2, '.', ',')}</div>
        </td>
        <td>
          <div className="text-right">kr. {accounting.format(unitPrice, 2, '.', ',')}</div>
        </td>
        <td>
          <div className="text-right">
            kr. {accounting.format(subtotal, 2, '.', ',')}
          </div>
        </td>
        <td className="text-right">
          <div className="btn-group">
            <button onClick={onEdit} className="btn btn-xs btn-default">
              <Icon name="edit" size={19} />
            </button>
            <button onClick={onDelete} className="btn btn-xs btn-default">
              <Icon name="delete" size={19} />
            </button>
          </div>
        </td>
    </tr>
  );
}

SubscriptionProduct.propTypes = {
  product: PropTypes.object.isRequired,
  onEdit: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};



