import React, { PropTypes } from 'react';
import cx from 'classnames';
import flatten from 'lodash/flatten';
import Group from './group';

export default function FormInput(props) {
  const { className, type, label, id, ...rest } = props;
  const classes = flatten([className]);

  if (!rest.onChange) {
    rest.onChange = () => {};
  }

  let content = (
    <div>
      <label style={{width: '100%'}} htmlFor={id}>{label}</label>
      <input
        {...rest}
        id={id}
        type={type}
        className={cx('form-control', className)}
      />
    </div>
  );

  if (props.wrap) {
    content = <Group {...props.group}>{content}</Group>;
  }

  return content;
}

FormInput.propTypes = {
  className: PropTypes.string,
  id: PropTypes.string,
  label: PropTypes.any,
  type: PropTypes.string.isRequired,
};

FormInput.defaultProps = {
  id: '',
  label: '',
  type: 'text',
  wrap: true,
};



