import React, { PropTypes } from 'react';
import { dkk } from 'app/lib/currency';
import Icon from 'ui/icon';
import { DestinationTypes } from 'app/constants/tele';

const EditButtons = ({ onEdit, onDelete }) => {
  if (!onEdit && !onDelete) {
    return null;
  }

  return (
    <td className="text-right">
      <button
        onClick={onEdit}
        className="btn btn-sm btn-default push-5-r"
      >
        <Icon name="edit" />
      </button>
      <button
        onClick={onDelete}
        className="btn btn-sm btn-default"
      >
        <Icon name="delete" />
      </button>
    </td>
  );
};

EditButtons.propTypes = {
  onEdit: PropTypes.func,
  onDelete: PropTypes.func,
};

const AgreementProduct = ({
  id,
  description,
  onDelete,
  onEdit,
  prefix,
  destinationType,
  navProductNumber,
  navProductPrice,
  unitPrice,
  unitType,
}) => (
  <tr>
    <td className="text-center">{id}</td>
    <td className="text-center">{navProductNumber}</td>
    <td>{description}</td>
    <td className="text-center">{prefix}</td>
    <td className="text-center">{DestinationTypes[destinationType]}</td>
    <td className="text-center">{unitType}</td>
    <td className="text-right">{dkk(navProductPrice)}</td>
    <td className="text-right">{dkk(unitPrice)}</td>
    <EditButtons
      onEdit={onEdit}
      onDelete={onDelete}
    />
  </tr>
);

AgreementProduct.propTypes = {
  description: PropTypes.string.isRequired,
  onDelete: PropTypes.func,
  onEdit: PropTypes.func,
  prefix: PropTypes.string.isRequired,
  navProductNumber: PropTypes.string.isRequired,
  destinationType: PropTypes.number.isRequired,
  navProductPrice: PropTypes.number.isRequired,
  unitPrice: PropTypes.number.isRequired,
  unitType: PropTypes.string.isRequired,
};

export default AgreementProduct;



