import React, { PropTypes } from 'react';
import k from 'app/constants';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import Icon from 'ui/icon';
import { Link } from 'react-router';

const Heading = ({ text }) => (
  <li className="nav-main-heading">
    <span className="sidebar-mini-hide">{text}</span>
  </li>
);

Heading.propTypes = {
  text: PropTypes.string.isRequired,
};

const MenuItem = ({ text, icon, to }) => (
  <li>
    <Link to={`${k.BASEURL}${to}`.replace(/\/\//, '/')}>
      <Icon name={icon} size="sm" /> {text}
    </Link>
  </li>
);

MenuItem.propTypes = {
  text: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  to: PropTypes.string.isRequired,
};

/**
 * Menu component
 * @extend {ReactComponent}
 */
class Menu extends React.Component {
  static stylesheet = require('./menu.scss');

  render() {
    return (
      <ul className="nav-main">
        <Heading text="Hosting Billing" />
        <MenuItem
          text="Subscriptions"
          icon="date_range"
          to="/hosting/subscriptions/"
        />
        <MenuItem
          text="New Subscription"
          icon="add"
          to="/hosting/subscriptions/new"
        />

        <Heading text="IP Vision" />
        <MenuItem
          text="Agreements"
          icon="date_range"
          to="/tele/agreements/"
        />

        <Heading text="WHMCS" />
        <MenuItem
          text="import"
          icon="list"
          to="/whmcs"
        />

        <Heading text="TimeLog" />
        <MenuItem
          text="import"
          icon="list"
          to="/timelog"
        />

        <Heading text="Unitel" />
      </ul>
    );
  }
}

export default compose(
  Menu,
  isStyleable
);



