import pick from 'lodash/pick';
import axios from 'axios';

import k from 'app/constants';
import { errorHandler, fetchJsonOnSuccess } from 'app/lib/fetch-utils';

/**
 * Load a subscription with a specific id
 * @param  {number} id
 * @return {Function}
 */
export function loadAgreement(id: number) {
  return (dispatch, getState) => {
    dispatch({ type: k.TELE_AGREEMENT_LOAD });

    setTimeout(() => {
      const state = getState().teleAgreements.entries;
      let agreement = state.find(e => e.id === id);
      agreement = JSON.parse(JSON.stringify(agreement));

      dispatch({ type: k.TELE_AGREEMENT_LOADED, agreement });
    }, 1);
  };
}

/**
 * Cancels an agreement
 * @param  {Object} id
 * @return {Function}
 */
export function cancelAgreement(id: number) {
  return (dispatch) => {
    dispatch({ type: k.TELE_AGREEMENT_CANCEL });

    axios.delete(`${k.API_HOST}/api/teleAgreements/${id}`)
      .then(response => {
        const agreement = response.data;
        dispatch({ type: k.TELE_AGREEMENT_CANCELLED, agreement });
      })
      .catch(errorHandler);
  };
}

/**
 * Persist the agreement on the server
 * @param  {Number} id
 * @param  {Object} agreement
 * @return {Function}
 */
export function persistAgreement(id: number, value: Object) {
  return (dispatch) => {
    dispatch({ type: k.TELE_AGREEMENT_PERSIST });

    const agreement = pick(value, [
      'id',
      'description',
      'navCustomerId',
      'navCustomerName',
      'numbers',
    ]);

    axios.put(`${k.API_HOST}/api/teleAgreements/${id}`, agreement)
      .then(response => {
        dispatch({
          type: k.TELE_AGREEMENT_PERSISTED,
          agreement: response.data,
        });
      })
      .catch(errorHandler);
  };
}

/**
 * Resets the agreement to it's default state
 * @return {Object}
 */
export function newAgreement() {
  return {
    type: k.TELE_AGREEMENT_NEW,
  };
}

/**
 * Create the new agreement
 * @param  {Object} agreement
 */
export function createAgreement(value: Object) {
  return (dispatch) => {
    dispatch({ type: k.TELE_AGREEMENT_CREATE });

    const agreement = pick(value, [
      'description',
      'navCustomerId',
      'navCustomerName',
      'numbers',
    ]);

    axios.post(`${k.API_HOST}/api/teleAgreements`, agreement)
      .then(response => {
        dispatch({
          type: k.TELE_AGREEMENT_CREATED,
          agreement: response.data,
        });
      })
      .catch(errorHandler);
  };
}

/**
 * Update the agreement
 * @param  {Object} fields
 * @return {Object}
 */
export function updateAgreement(fields: Object) {
  return {
    type: k.TELE_AGREEMENT_UPDATE,
    fields,
  };
}

/**
 * Delete a number series
 * @param  {Number} id
 * @return {Object}
 */
export function deleteNumber(id: number) {
  return {
    type: k.TELE_AGREEMENT_NUMBER_REMOVE,
    id,
  };
}

/**
 * Udate a number series
 * @param  {Object} number
 * @return {Object}
 */
export function updateNumber(number: Object) {
  return {
    type: k.TELE_AGREEMENT_NUMBER_UPDATE,
    number,
  };
}

/**
 * Udate a number series
 * @param  {Object} number
 * @return {Object}
 */
export function insertNumber(number: Object) {
  return {
    type: k.TELE_AGREEMENT_NUMBER_INSERT,
    number,
  };
}

/**
 * Removes a product from the given number series
 * @param  {Number} number
 * @param  {Number} product
 * @return {Object}
 */
export function removeNumberProduct(numberId: number, productId: number) {
  return {
    type: k.TELE_AGREEMENT_NUMBER_PRODUCT_REMOVE,
    numberId,
    productId,
  };
}

/**
 * Update a number series product
 * @param  {Object} product
 * @return {Object}
 */
export function updateNumberProduct(numberId: number, product: Object) {
  return {
    type: k.TELE_AGREEMENT_NUMBER_PRODUCT_UPDATE,
    numberId,
    product,
  };
}

/**
 * Insert a number series product
 * @param  {Object} product
 * @return {Object}
 */
export function insertNumberProduct(numberId: number, product: Object) {
  return {
    type: k.TELE_AGREEMENT_NUMBER_PRODUCT_INSERT,
    numberId,
    product,
  };
}



