import moment from 'moment';

export function nextDateInCycle(date, cycle) {
  switch (cycle) {
    case 'Monthly': {
      return moment(date).add(1, 'months');
    }
    case 'Quaterly': {
      return moment(date).add(3, 'months');
    }
    case 'Biannually': {
      return moment(date).add(6, 'months');
    }
    case 'Annually': {
      return moment(date).add(12, 'months');
    }
  }

  return moment(date);
}



