import React, { PropTypes } from 'react';

export default function Grid({ children, columns, size }) {
  let content = children;

  // If a preset column list has been set, the children gets wrapped
  // in a column.
  if (columns) {
    let cls = `col-${size}-${12 / columns}`;
    content = React.Children.map(children, el => {
      if (el.props.column) {
        cls = `col-${size}-${el.props.column}`;
      }

      return <div className={cls}>{el}</div>;
    });
  }

  return (
    <div className="grid">
      <div className="row">
        { content }
      </div>
    </div>
  );
}

Grid.propTypes = {
  children: PropTypes.any,
  columns: PropTypes.number.isRequired,
  size: PropTypes.string.isRequired,
};

Grid.defaultProps = {
  columns: 1,
  size: 'lg',
};



