import k from 'app/constants';
import _ from 'lodash';
import axios from 'axios';

/**
 * Stores a cached version, if needed, in the local storage
 * @param  {String} name
 * @param  {Object} request
 */
function writeCache(name: string, request: Object, expires: number = 60) {
  // Don't write if the expires key is already present
  if (request._cached) {
    return;
  }

  // Expires in the future
  const d = new Date();
  d.setMinutes(d.getMinutes() + expires);

  // Write the new value
  const value = request;
  value._cached = d.getTime();
  window.localStorage[name] = JSON.stringify(value);
}

/**
 * Read a request from local storage
 * @param  {String} name
 * @return {Object}
 */
function readCache(name: string) {
  if (!window.localStorage[name]) {
    return null;
  }

  const value = JSON.parse(window.localStorage[name]);
  const now = (new Date).getTime();

  if (now < value._cached) {
    return value;
  }

  return null;
}

/**
 * Creates a fetch request, or returns a cached version
 * @return {Promise}
 */
function fetchNavisionCustomers(): Promise {
  const cached = readCache('customers');
  if (cached) {
    return Promise.resolve(cached);
  }

  return axios.get(`${k.API_HOST}/api/nav/customers`);
}

/**
 * Creates a fetch request, or returns the cached version
 * @return {Promise}
 */
function fetchNavisionProducts(): Promise {
  const cached = readCache('products');
  if (cached) {
    return Promise.resolve(cached);
  }

  return axios.get(`${k.API_HOST}/api/nav/products`);
}

/**
 * @param  {Object} payload
 * @return {Object}
 */
export function hydrateNavision(): Function {
  return (dispatch) => {
    dispatch({ type: k.NAVISION_HYDRATE });

    Promise.all([
      fetchNavisionCustomers(),
      fetchNavisionProducts(),
    ]).then(results => {
      const codes = _.uniq(_.map(results, _.property('status')));
      if (_.isEqual(codes, [200])) {
        writeCache('customers', results[0], 60);
        writeCache('products', results[1], 60);

        dispatch({
          type: k.NAVISION_HYDRATED,
          customers: results[0].data,
          products: results[1].data,
        });
      }
    });
  };
}




