import React, { PropTypes } from 'react';
import k from 'app/constants';
import { Link } from 'react-router';

/**
 * Entry component
 * @extend {ReactComponent}
 */
const AgreementListEntry = ({ agreement }) => (
  <tr>
    <td>
      {agreement.navCustomerName}
    </td>
    <td>
      <Link to={`${k.BASEURL}tele/agreements/${agreement.id}`}>
        {agreement.description}
      </Link>
    </td>
    <td className="text-center">
      {agreement.numbers.length}
    </td>
  </tr>
);

AgreementListEntry.propTypes = {
  agreement: PropTypes.shape({
    navCustomerName: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
  }),
};

export default AgreementListEntry;



