import React, { PropTypes } from 'react';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import isConnectable from 'app/concerns/connectable';
import filter from 'lodash/filter';
import head from 'lodash/head';
import Grid from 'ui/oneui/grid';

import Products from './products';
import Actions from './actions';
import Details from './details';
import History from './history';

/**
 * Subscriptions component
 * @extend {ReactComponent}
 */
class SubscriptionShow extends React.Component {
  static stylesheet = require('./styles.scss');

  static storeProps = {
    subscriptions: v => v.hostingSubscriptions.entries.toJS(),
  }

  static propTypes = {
    subscriptions: PropTypes.array.isRequired,
    params: PropTypes.object,
  }

  render() {
    const id = parseInt(this.props.params.id, 10);
    const sub = head(filter(this.props.subscriptions, { id }));
    if (!sub) {
      return null;
    }

    return (
      <div className="subscription">
        <div className="content">
          <Actions
            subscription={sub}
          />

          <Products
            items={sub.products}
          />

          <Grid columns={2}>
            <Details
              subscription={sub}
            />
            <History
              audits={sub.auditHistory}
            />
          </Grid>
        </div>
      </div>
    );
  }
}

export default compose(
  SubscriptionShow,
  isStyleable,
  isConnectable
);



