import { List } from 'immutable';
import k from 'app/constants';

/**
 * Define the shape of the reducers state
 * @type {FlowType}
 */
type State = {
  customers: List,
  products: List,
  fetching: boolean,
  hydrated: boolean,
};

/**
 * Define the initial/clean state for the reducer
 * @type {FlowType}
 */
const initialState: State = {
  customers: List(),
  products: List(),
  fetching: false,
  hydrated: false,
};

/**
 * customers reducer
 * @param  {Object} state
 * @param  {Object} action
 * @return {Object}
 */
export default function reducer(state: State = initialState, action: Object): State {
  switch (action.type) {
    case k.NAVISION_HYDRATE: {
      return { ...state, fetching: true };
    }

    case k.NAVISION_HYDRATED: {
      const customers = List(action.customers);
      const products = List(action.products);
      return { ...state, fetching: false, hydrated: true, customers, products };
    }

    default: {
      return state;
    }
  }
}



