import React, { PropTypes } from 'react';
import last from 'lodash/last';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import SubscriptionForm from '../form';
import PageHeader from 'ui/oneui/page-header';
import { resetSubscription, createSubscription } from 'app/actions/hosting/subscription';
import { push } from 'app/actions/routing';

class SubscriptionsNew extends React.Component {
  static storeActions = {
    resetSubscription,
    createSubscription,
  }

  static storeProps = {
    posting: v => v.hostingSubscription.posting,
    subscriptions: v => v.hostingSubscriptions.entries.toJS(),
    fields: v => v.hostingSubscription.fields.toJS(),
    products: v => v.hostingSubscription.products.toJS(),
  }

  static propTypes = {
    actions: PropTypes.object.isRequired,
    posting: PropTypes.bool,
    fields: PropTypes.object,
    products: PropTypes.any,
  }

  componentDidMount() {
    this.props.actions.resetSubscription();
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.posting && !nextProps.posting) {
      const product = last(nextProps.subscriptions);
      push(`/hosting/subscriptions/${product.id}`);
    }
  }

  handleOnSubmit = () => {
    const fields = this.props.fields;

    if (!fields.navCustomerId) {
      alert('Please select a customer');
      return;
    }

    if (!fields.description) {
      alert('A subscription requires a description');
      return;
    }

    this.props.actions.createSubscription({
      fields,
      products: this.props.products,
    });
  }

  handleOnReset = () => {
    this.props.actions.resetSubscription();
  }

  render() {
    return (
      <div className="subscription">
        <PageHeader
          title="New Subscription"
        />
        <SubscriptionForm
          fields={this.props.fields}
          products={this.props.products}
          onSubmit={this.handleOnSubmit}
          onReset={this.handleOnReset}
        />
      </div>
    );
  }
}

export default compose(
  SubscriptionsNew,
  isConnectable
);



