import React, { PropTypes } from 'react';
import moment from 'moment';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import Icon from 'ui/icon';

/**
 * DatePicker component
 * @extend {ReactComponent}
 */
class DatePicker extends React.Component {
  static stylesheet = require('./date-picker.scss');

  static propTypes = {
    value: PropTypes.object,
    onSelect: PropTypes.func.isRequired,
    onDismiss: PropTypes.func.isRequired,
  }

  constructor(props) {
    super(props);

    this.state = {
      current: moment(this.props.value),
      selected: moment(this.props.value),
    };
  }

  handleNextMonth = () => {
    const current = moment(this.state.current).add(1, 'month');
    this.setState({ current });
  }

  handlePrevMonth = () => {
    const current = moment(this.state.current).add(-1, 'month');
    this.setState({ current });
  }

  handleSetDay = (day) => {
    const current = moment(this.state.current).date(day);
    this.setState({
      current,
      selected: current,
    });
  }

  handleOnSelect = () => {
    this.props.onSelect(this.state.selected);
  }

  handleToday = () => {
    const current = moment();
    this.setState({ current, selected: moment() });
  }

  render() {
    const { current, selected } = this.state;

    const days = [];
    for (let i = 1; i <= current.daysInMonth(); i++) {
      const active = selected.clone().format('YYYY-MM-DD') ===
                     current.clone().date(i).format('YYYY-MM-DD');
      days.push(
        <div
          key={i}
          className={['calendar-day', {
            'calendar-day--active': active,
          },
          ]}
          onClick={this.handleSetDay.bind(null, i)}
        >
          {i}
        </div>
      );
    }

    return (
      <div className="date-picker">
        <div className="container">
          <div className="header">
            <div className="header-year">
              {current.year()}
            </div>
            <div className="header-date">
              {current.format('ddd, MMM Do')}
            </div>
          </div>
          <div className="calendar">
            <div className="calendar-month">
              <div onClick={this.handlePrevMonth} className="calendar-month-prev">
                <Icon name="keyboard_arrow_left" size="md" />
              </div>
              <div className="calendar-month-current">
                {current.format('MMMM YYYY')}
              </div>
              <div onClick={this.handleNextMonth} className="calendar-month-next">
                <Icon name="keyboard_arrow_right" size="md" />
              </div>
            </div>
            <div className="calendar-days">
              { days }
            </div>
          </div>
          <div className="footer">
            <button
              className="btn btn-default pull-left"
              onClick={this.props.onDismiss}
            >
              Cancel
            </button>
            <button
              className="btn btn-success pull-right"
              onClick={this.handleOnSelect}
            >
              Select
            </button>
            <button
              className="btn btn-default pull-right"
              onClick={this.handleToday}
              style={{marginRight: 10}}
            >
              Today
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default compose(
  DatePicker,
  isStyleable
);



