import { List, fromJS } from 'immutable';
import k from 'app/constants';

/**
 * Define the shape of the reducers state
 * @type {FlowType}
 */
type State = {
  fetching: boolean,
  hydrated: boolean,
  entries: List,
};

/**
 * Define the initial/clean state for the reducer
 * @type {FlowType}
 */
const initialState: State = {
  fetching: false,
  hydrated: false,
  entries: List(),
};

/**
 * Agreements reducer
 * @param  {Object} state
 * @param  {Object} action
 * @return {Object}
 */
export default function reducer(state: State = initialState, action: Object): State {
  switch (action.type) {
    case k.TELE_AGREEMENTS_HYDRATE: {
      return { ...state, fetching: true };
    }

    case k.TELE_AGREEMENTS_HYDRATED: {
      const entries = List(action.agreements);
      return { ...state, entries, fetching: false, hydrated: true };
    }

    case k.TELE_AGREEMENT_CREATED: {
      const agreement = fromJS(action.agreement).toJS();
      const entries = state.entries.push(agreement);
      return { ...state, entries };
    }

    case k.TELE_AGREEMENT_PERSISTED: {
      const idx = state.entries.findIndex(e => e.id === action.agreement.id);

      const agreement = fromJS(action.agreement).toJS();
      const entries = state.entries.set(idx, agreement);
      return { ...state, entries };
    }

    case k.TELE_AGREEMENT_CANCELLED: {
      const index = state.entries.findIndex(x => x.id === action.agreement.id);
      const entries = state.entries.delete(index);

      return { ...state, entries };
    }

    default: {
      return state;
    }
  }
}



