import React, { PropTypes } from 'react';
import map from 'lodash/map';
import reduce from 'lodash/reduce';
import Block from 'ui/oneui/block';
import accounting from 'accounting';

import Product from './product';

/**
 * Products component
 * @extend {ReactComponent}
 */
class Products extends React.Component {
  static propTypes = {
    items: PropTypes.array,
  }

  render() {
    const children = map(this.props.items, p => (
      <Product key={p.id} {...p} />
    ));

    const subtotal = reduce(
      map(this.props.items, p => p.unitPrice * p.unitAmount),
      (a, b) => a += b
    );

    return (
      <div className="content-grid">
        <Block
          title="Products"
          titleClassName="bg-gray-lighter"
        >
          <table className="table table-products table-borderless table-striped table-vcenter">
            <thead>
              <tr>
                <th className="text-center">ID</th>
                <th>Product Name</th>
                <th className="text-right">Unit Amount</th>
                <th>Unit Type</th>
                <th className="text-right">NAV Price</th>
                <th className="text-right">Unit Price</th>
                <th className="text-right">Subtotal</th>
              </tr>
            </thead>
            <tbody>
              {children}
              <tr className="success" role="summary">
                <td className="text-right text-uppercase" colSpan={6}>
                  <strong>Total:</strong>
                </td>
                <td className="text-right">
                  <b>kr. {accounting.format(subtotal, 2, '.', ',')}</b>
                </td>
              </tr>
            </tbody>
          </table>
        </Block>
      </div>
    );
  }
}

export default Products;



