import k from 'app/constants';
import { browserHistory } from 'react-router';

/**
 * A simple action object creator
 * @param  {Object} location
 * @return {Object}
 */
export function locationChange(location: Object) {
  return {
    type: k.ROUTING_LOCATION_CHANGE,
    location,
  };
}

/**
 * Convience action for pushing to the history
 * @param {String} path
 */
export function push(path: string) {
  const url = `${k.BASEURL}${path}`.replace(/^\/\//, '/');
  browserHistory.push(url);
}



