/**
 * Wrap a component in an array of concerns, to create a composited
 * react component. Some of these will be based on inheritance, and
 * will be marked with a name.
 * @param  {ReactComponent}  component
 * @param  {Array<Function>} concerns
 * @return {ReactComponent}
 */
export default function compose(Component: Function, ...concerns) {
  // Preserve the original component, and alter a modified
  // version instead.
  let composite = Component;
  let composers = [];

  // Run through each of the concerns and apply them.
  for (let i = 0; i < concerns.length; i++) {
    // Get a reference to the next concern, and add it's name to the
    // list of composite composers.
    const fn = concerns[i];
    composers.push(fn.name);

    // Check if the concern will wrap the current composite, or if it
    // will extend it. If the component will get wrapped, we update
    // the display name for easier debugging.
    if (fn.willWrapComponent) {
      const names = composers.join(',');
      composite.displayName = `${composite.displayName}(${names})`;
      composers = [];
    }

    // Update the composite
    composite = fn(composite);
  }

  // If there are any remaning composer, they are added now
  if (composers.length) {
    const names = composers.join(',');
    composite.displayName = `${composite.displayName}(${names})`;
  }

  // Return the composed component
  return composite;
}



