import React, { PropTypes } from 'react';
import Block from 'ui/oneui/block';

import Product from './product';

export default class AgreementNumber extends React.Component {
  static propTypes = {
    description: PropTypes.string.isRequired,
    numbers: PropTypes.string.isRequired,
    products: PropTypes.object.isRequired,
  }

  render() {
    const numbers = this.props.numbers.replace(/\-/, ' - ').replace(/,/, ', ');

    return (
      <Block title={`${this.props.description}: ${numbers}`}>
        <table className="table table-products table-borderless table-striped table-vcenter">
          <thead>
            <tr>
              <th className="text-center">#</th>
              <th className="text-center">NAV</th>
              <th>Description</th>
              <th className="text-center">Prefix</th>
              <th className="text-center">Type</th>
              <th className="text-center">Unit Type</th>
              <th className="text-right">Nav Price</th>
              <th className="text-right">Unit Price</th>
            </tr>
          </thead>
          <tbody>
            {
              this.props.products.map(p => (
                <Product key={p.id} {...p} />
              ))
            }
          </tbody>
        </table>
      </Block>
    );
  }
}



