import React from 'react';
import compose from 'app/lib/compose';
import isStyleable from 'app/concerns/styleable';
import { Link } from 'react-router';
import Icon from 'ui/icon';

/**
 * Header component
 * @extend {ReactComponent}
 */
class Header extends React.Component {
  static stylesheet = require('./header.scss');

  render() {
    return (
      <div className="side-header side-content bg-white-op">
        <Link className="h5 text-white" to="/">
          <span className="h4 font-w600">
            <Icon name="monetization_on" /> RP Billing
          </span>
        </Link>
      </div>
    );
  }
}

export default compose(
  Header,
  isStyleable
);



