import React, { PropTypes } from 'react';
import moment from 'moment';
import sortBy from 'lodash/sortBy';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import Grid from 'ui/oneui/grid';
import Block from 'ui/oneui/block';
import Loading from 'ui/loading';

import { updateSubscription } from 'app/actions/hosting/subscription';

import CustomerDetails from './customer';
import BillingDetails from './billing';
import ProductsDetails from './products';
import Actions from './actions';

export class SubscriptionForm extends React.Component {
  static storeProps = {
    navision: v => ({
      customers: sortBy(v.navision.customers.toJS(), 'name'),
      products: v.navision.products.toJS(),
    }),
    isPosting: v => v.hostingSubscription.posting,
  }

  static storeActions = {
    updateSubscription,
  }

  static propTypes = {
    onSubmit: PropTypes.func.isRequired,
    onReset: PropTypes.func.isRequired,
    fields: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    navision: PropTypes.object.isRequired,
    isPosting: PropTypes.bool,
    products: PropTypes.any,
  }

  static defaultProps = {
    fields: {
      firstInvoice: moment(),
      nextInvoice: moment(),
    },
  }

  handleOnChange = (e, partials) => {
    let fields = {};
    if (partials) {
      fields = partials;
    } else {
      fields = { [e.target.name]: e.target.value };
    }

    this.props.actions.updateSubscription(fields);
  }

  render() {
    const shared = {
      navision: this.props.navision,
      onChange: this.handleOnChange,
      subscription: this.props.fields,
    };

    let loader;
    if (this.props.isPosting) {
      loader = (
        <Loading message="Saving changes ..." />
      );
    }

    console.log(this.props);
    return (
      <div className="content form-horizontal">
        {loader}
        <Grid columns={2} size="sm">
          <CustomerDetails
            column={5}
            {...shared}
          />
          <BillingDetails
            column={7}
            {...shared}
          />
        </Grid>
        <ProductsDetails
          {...shared}
          products={this.props.products}
        />
        <Block title="Additonal Info">
          <div className="block-content">
            <p>
              <textarea
                className="form-control"
                rows="3"
                name="additionalText"
                value={this.props.fields.additionalText || ''}
                onChange={this.handleOnChange}
              />
            </p>
            <p>
              <small>Appears at the bottom of every invoice</small>
            </p>
          </div>
        </Block>
        <Actions
          disabled={this.props.isPosting}
          onSubmit={this.props.onSubmit}
          onReset={this.props.onReset}
        />
      </div>
    );
  }
}

export default compose(
  SubscriptionForm,
  isConnectable
);



