import React, { PropTypes } from 'react';
import Icon from 'ui/icon';
import Block from 'ui/oneui/block';
import Grid from 'ui/oneui/grid';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import { cancelSubscription } from 'app/actions/hosting/subscription';
import { push } from 'app/actions/routing';
import Loading from 'ui/loading';

class SubscriptionActions extends React.Component {
  static storeProps = {
    isDeleting: v => v.hostingSubscription.deleting,
  }

  static storeActions = {
    cancelSubscription,
  }

  static propTypes = {
    isDeleting: PropTypes.bool,
    actions: PropTypes.object,
    subscription: PropTypes.object,
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.isDeleting && !nextProps.isDeleting) {
      push('/hosting/subscriptions');
    }
  }

  handleCancelSubscription = () => {
    if (confirm('Are you sure you wan\'t to cancel this subscription ?')) {
      this.props.actions.cancelSubscription(this.props.subscription.id);
    }
  }

  render() {
    const { subscription } = this.props;

    let loading = null;
    if (this.props.isDeleting) {
      loading = (<Loading message="The subscription is being deleted ..." />);
    }
    return (
      <div>
        {loading}
        <Grid columns={2} size="sm">
          <Block
            link={`/hosting/subscriptions/${subscription.id}/edit`}
            className="text-center font-w700"
          >
            <div className="text-success"><Icon name="edit" size={43} /></div>
            <div
              className="block-content block-content-full block-content-mini
              bg-gray-lighter text-success"
            >
              Edit <span className="hidden-xs hidden-sm hidden-md">Subscription</span>
            </div>
          </Block>
          <Block onClick={this.handleCancelSubscription} className="text-center font-w700">
            <div className="text-danger"><Icon name="delete" size={43} /></div>
            <div
              className="block-content block-content-full
              block-content-mini bg-gray-lighter text-danger"
            >
              Cancel <span className="hidden-xs hidden-sm hidden-md">Subscription</span>
            </div>
          </Block>
        </Grid>
      </div>
    );
  }
}

export default compose(
  SubscriptionActions,
  isConnectable,
);



