import React, { PropTypes } from 'react';
import Block from 'ui/oneui/block';
import FormSelect from 'ui/oneui/form/select';
import FormInput from 'ui/oneui/form/input';

export default function SubscriptionCustomerDetails({ subscription, navision, onChange }) {
  function handleCustomerChange(e) {
    const customer = navision.customers[e.target.selectedIndex - 1];
    const result = {
      navCustomerId: customer.number,
      navCustomerName: customer.name,
    };

    onChange(e, result);
  }

  return (
    <Block title="Customer">
      <div className="block-content">
        <FormSelect
          name="customer"
          label="Customer"
          prompt="..."
          value={subscription.navCustomerId}
          mapping={{ value: 'number', text: 'name' }}
          options={navision.customers}
          onChange={handleCustomerChange}
        />
        <FormInput
          name="description"
          value={subscription.description}
          label="Description"
          onChange={onChange}
        />
      </div>
    </Block>
  );
}

SubscriptionCustomerDetails.propTypes = {
  subscription: PropTypes.object.isRequired,
  navision: PropTypes.object.isRequired,
  onChange: PropTypes.func,
};



