import React, { PropTypes } from 'react';
import k from 'app/constants';
import { Link } from 'react-router';
import accounting from 'accounting';
import map from 'lodash/map';
import reduce from 'lodash/reduce';

/**
 * Entry component
 * @extend {ReactComponent}
 */
export default class Entry extends React.Component {
  static propTypes = {
    subscription: PropTypes.shape({
      id: PropTypes.number.isRequired,
      description: PropTypes.string.isRequired,
      billingCycle: PropTypes.string.isRequired,
      firstInvoice: PropTypes.object.isRequired,
      nextInvoice: PropTypes.object.isRequired,
      products: PropTypes.array.isRequired,
    }),
  }

  render() {
    const sub = this.props.subscription;
    const subtotal = reduce(
      map(
        sub.products,
        p => p.unitAmount * p.unitPrice
      ),
      (a, b) => a + b
    );

    return (
      <tr>
        <td>
          {sub.navCustomerName}
        </td>
        <td>
          <Link to={`${k.BASEURL}hosting/subscriptions/${sub.id}`}>
            {sub.description}
          </Link>
        </td>
        <td>{sub.firstInvoice.format('DD/MM - YYYY')}</td>
        <td>{sub.nextInvoice.format('DD/MM - YYYY')}</td>
        <td>
          <div className="text-center">{sub.billingCycle}</div>
        </td>
        <td>
          <div className="text-right">{accounting.formatMoney(subtotal, 'kr. ', 2, ',', '.')}</div>
        </td>
      </tr>
    );
  }
}



