import { fromJS } from 'immutable';
import loggerFactory from 'redux-logger';

/**
 * Create a custom version of the logger
 * @type {Object}
 */
const logger = loggerFactory({
  /**
   * Print the duration of each action
   * @type {Boolean}
   */
  duration: true,

  /**
   * Collapses the log output, so it doesn't take up the entire screen
   * @type {Boolean}
   */
  collapsed: true,

  /**
   * Transform the action before printing it, ensuring the symbol part
   * gets removed.
   * @param  {Object} action
   * @return {Object}
   */
  actionTransformer: (action: Object) => {
    let type = String(action.type);

    const matches = type.match(/Symbol\(([^\)]+)/);
    if (matches && matches.length === 2) {
      type = matches[1];
    }

    return { ...action, type };
  },

  /**
   * Transform the state from an immutable state into a
   * plain js object.
   * @param  {Object} state
   * @return {Object}
   */
  stateTransformer: (state: Object) => (
    fromJS(state).toJS()
  ),
});

// Export the middleware
export default logger;



