import React from 'react';
import classnames from 'classnames';
import map from 'lodash/map';

function resolve(tree, node) {
  // If the passed node isn't a proper react element, there is no reason
  // for the resolve function to continue,
  if (!React.isValidElement(node)) {
    return node;
  }

  // Extract the properties we are going to change, before using it
  // to clone the node.
  let { className, children } = node.props;

  // Attempt to resolve the className with the styles tree
  if (className) {
    className = map(
      classnames(className).split(' '),
      selector => tree[selector] || selector
    ).join(' ');
  }

  // If the node has any children, they need to be resolved aswell
  if (React.Children.count(children)) {
    children = React.Children.map(children, c => resolve(tree, c));
  }

  // Return the cloned element
  return React.cloneElement(node, { className }, children);
}

/**
 * Overrides the current shouldComponentUpdate method with something
 * that does a quick compparioson of props and state
 * @param  {ReactComponent} Component
 * @return {ReactComponent}
 */
function isStyleable(Component) {
  // Create a local reference to the component to prevent lint from
  // complaing about assignment.
  const clone = Component;

  // If the components prototype doesn't contain a method for getting
  // the components class name tree, we don't need to continue.
  if (typeof clone.stylesheet !== 'object') {
    if (process.env.NODE_ENV === 'development') {
      console.error('isStyleable(): Component is missing a static stylesheet property.');
    }

    return clone;
  }

  // Check if the passed component contains a life cycle method.
  // I don't know if there is a better way to test for stateless
  // functions.
  if (clone.prototype.render === undefined) {
    const styleableStatelessRender = function styleableStatelessTryRender(props) {
      const el = clone.call(clone, props);
      return resolve(clone.stylesheet, el);
    };

    styleableStatelessRender.displayName = clone.name;
    return styleableStatelessRender;
  }

  // Override the default render method in order to resolve class names
  const render = clone.prototype.render;
  clone.prototype.render = function styleableRender() {
    const el = render.call(this);
    return resolve(clone.stylesheet, el);
  };

  // Return the altered object;
  return clone;
}

/**
 * Convience method of wrapping a stateless component
 * @param  {Object} stylesheet
 * @return {ReactComponent}
 */
isStyleable.using = (stylesheet) => (
  (Component) => {
    const clone = Component;
    clone.stylesheet = stylesheet;

    return isStyleable(clone);
  }
);

export default isStyleable;



