import React, { PropTypes } from 'react';
import Block from 'ui/oneui/block';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import {
  removeNumberProduct,
  updateNumberProduct,
  insertNumberProduct,
  deleteNumber,
  updateNumber,
} from 'app/actions/tele/agreement';

import Product from '../show/product';
import ProductFrom from './product-form';
import NumberForm from './number-form';

class AgreementFormNumber extends React.Component {
  static propTypes = {
    actions: PropTypes.object.isRequired,
    number: PropTypes.object.isRequired,
    products: PropTypes.array.isRequired,
    onChange: PropTypes.func.isRequired,
  }

  static storeActions = {
    removeNumberProduct,
    updateNumberProduct,
    insertNumberProduct,
    deleteNumber,
    updateNumber,
  }

  state = {
    editingProduct: null,
    isEditing: false,
  }

  // PRODUCT ACTIONS

  handleOnProductAdd = (e) => {
    e.preventDefault();
    this.setState({
      editingProduct: {
        id: 0,
        teleNumberSeriesId: this.props.number.id,
        index: -1,
      },
    });
  }

  handleOnProductEdit = (p, i, e) => {
    e.preventDefault();

    const editingProduct = p;
    editingProduct.index = i;
    this.setState({ editingProduct });
  }

  handleOnProductDelete = (idx, e) => {
    e.preventDefault();
    this.props.actions.removeNumberProduct(this.props.number.id, idx);
  }

  handleOnProductEditSave = (e, product) => {
    e.preventDefault();

    if (product.index > -1) {
      this.props.actions.updateNumberProduct(this.props.number.id, product);
    } else {
      this.props.actions.insertNumberProduct(this.props.number.id, product);
    }

    this.setState({ editingProduct: null });
  }

  handleOnProductEditDismiss = (e) => {
    e.preventDefault();
    this.setState({ editingProduct: null });
  }

  // NUMBER ACTIONS

  handleOnNumberDelete = (e) => {
    e.preventDefault();
    this.props.actions.deleteNumber(this.props.number.id);
  }

  handleOnNumberEdit = (e) => {
    e.preventDefault();
    this.setState({ isEditing: true });
  }

  handleOnNumberEditSave = (e, number) => {
    e.preventDefault();
    this.setState({ isEditing: false });

    this.props.actions.updateNumber(number);
  }

  handleOnNumberEditDismiss = (e) => {
    e.preventDefault();
    this.setState({ isEditing: false });
  }

  render() {
    const { id, description, numbers, products } = this.props.number;

    let productForm;
    if (this.state.editingProduct) {
      productForm = (
        <ProductFrom
          product={this.state.editingProduct}
          products={this.props.products}
          onSave={this.handleOnProductEditSave}
          onDismiss={this.handleOnProductEditDismiss}
        />
      );
    }

    let numberForm;
    if (this.state.isEditing) {
      numberForm = (
        <NumberForm
          onSave={this.handleOnNumberEditSave}
          onDismiss={this.handleOnNumberEditDismiss}
          number={this.props.number}
        />
      );
    }

    return (
      <Block title={`#${id} ${description}: ${numbers}`}>
        {numberForm}
        {productForm}
        <div className="block-content">
          <table className="table table-products table-borderless table-striped table-vcenter">
            <thead>
              <tr>
                <th className="text-center">#</th>
                <th className="text-center">NAV</th>
                <th>Description</th>
                <th className="text-center">Prefix</th>
                <th className="text-center">Type</th>
                <th className="text-center">Unit Type</th>
                <th className="text-right">Nav Price</th>
                <th className="text-right">Unit Price</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {
                products.map((p, i) => (
                  <Product
                    key={i}
                    onEdit={this.handleOnProductEdit.bind(this, p, i)}
                    onDelete={this.handleOnProductDelete.bind(this, i)}
                    {...p}
                  />
                ))
              }
            </tbody>
          </table>
        </div>
        <div className="block-content block-content-full">
          <button
            className="btn btn-default"
            style={{marginRight: 8}}
            onClick={this.handleOnNumberEdit}
          >
            Edit Number Series
          </button>
          <button
            onClick={this.handleOnNumberDelete}
            className="btn btn-default"
            style={{marginRight: 8}}
          >
            Delete Number Series
          </button>
          <button
            onClick={this.handleOnProductAdd}
            className="btn btn-default"
          >
            Add Product
          </button>
        </div>
      </Block>
    );
  }
}

export default compose(
  AgreementFormNumber,
  isConnectable,
);



