import React, { PropTypes } from 'react';
import Block from 'ui/oneui/block';

export default class AgreementDetails extends React.Component {
  static propTypes = {
    agreement: PropTypes.object.isRequired,
  }

  render() {
    const { navCustomerName, description } = this.props.agreement;
    return (
      <div>
        <Block
          title="Details"
          className="font-w700"
        >
          <div className="block-content">
            <table className="table table-borderless">
              <tbody>
                <tr>
                  <td className="text-muted" width={120}>Customer</td>
                  <td><b>{navCustomerName}</b></td>
                </tr>
                <tr>
                  <td className="text-muted" width={120}>Description</td>
                  <td><b>{description}</b></td>
                </tr>
              </tbody>
            </table>
          </div>
        </Block>
      </div>
    );
  }
}



