import React, { PropTypes } from 'react';
import { hydrateSubscriptions } from 'app/actions/hosting/subscriptions';
import compose from 'app/lib/compose';
import isConnectable from 'app/concerns/connectable';
import Loading from 'ui/loading';

class Hosting extends React.Component {
  static storeProps = {
    subscriptions: v => v.hostingSubscriptions,
  }

  static storeActions = {
    hydrateSubscriptions,
  }

  static propTypes = {
    actions: PropTypes.object,
    children: PropTypes.any,
    subscriptions: PropTypes.object,
  }

  componentDidMount() {
    if (!this.props.subscriptions.hydrated) {
      this.props.actions.hydrateSubscriptions();
    }
  }

  render() {
    if (!this.props.subscriptions.hydrated) {
      return <Loading noFadeIn />;
    }

    return (
      <div>
        {this.props.children}
      </div>
    );
  }
}

export default compose(
  Hosting,
  isConnectable
);



