import { List } from 'immutable';
import k from 'app/constants';

/**
 * Define the shape of the reducers state
 * @type {FlowType}
 */
type State = {
  fetching: boolean,
  hydrated: boolean,
  entries: List,
};

/**
 * Define the initial/clean state for the reducer
 * @type {FlowType}
 */
const initialState: State = {
  fetching: false,
  hydrated: false,
  entries: List(),
};

/**
 * subscriptions reducer
 * @param  {Object} state
 * @param  {Object} action
 * @return {Object}
 */
export default function reducer(state: State = initialState, action: Object): State {
  switch (action.type) {
    case k.HOSTING_SUBSCRIPTION_CREATE_SUCCESS: {
      const entries = state.entries.push(action.subscription);
      return { ...state, entries };
    }

    case k.HOSTING_SUBSCRIPTION_PERSIST_SUCCESS: {
      const index = state.entries.findIndex(v => v.id === action.subscription.id);
      const entries = state.entries.update(index, () => action.subscription);
      return { ...state, entries };
    }

    case k.HOSTING_SUBSCRIPTIONS_HYDRATE: {
      return { ...state, fetching: true };
    }

    case k.HOSTING_SUBSCRIPTIONS_HYDRATED: {
      const entries = List(action.subscriptions);
      return { ...state, entries, fetching: false, hydrated: true };
    }

    case k.HOSTING_SUBSCRIPTION_CANCEL_SUCCESS: {
      const index = state.entries.findIndex(v => v.id === action.id);
      const entries = state.entries.delete(index);
      return { ...state, entries };
    }

    default: {
      return state;
    }
  }
}



