/**
 * Simple helper method, designed to work with lodash's functions, such
 * as filter, reject, map ect.
 */

/**
 * Converts an object to an uppercased string, and wraps it in a symbol
 * @param  {Mixed} val
 * @return {Symbol}
 */
export function toSymbol(val) {
  return Symbol(val.toString().toUpperCase());
}



