import React, { PropTypes } from 'react';
import Block from 'ui/oneui/block';
import map from 'lodash/map';
import sortBy from 'lodash/sortBy';
import moment from 'moment';

export default function SubscriptionHistory({ audits }) {
  return (
    <Block
      title="Audit History"
      titleClassName="bg-gray-lighter"
    >
      <div className="block-content" style={{ padding: 0 }}>
        <table className="table table-vcenter">
          <tbody>
            {
              map(sortBy(audits, 'created').reverse(), (e, i) => (
                <tr key={i}>
                  <td>{e.description}</td>
                  <td className="text-muted" width={120}>
                    <small className="text-muted">
                      {moment(e.created).fromNow()}<br />
                      <small>{moment(e.created).format('YYYY-MM-DD HH:mm')}</small>
                    </small>
                  </td>
                </tr>
              ))
            }
          </tbody>
        </table>
      </div>
    </Block>
  );
}

SubscriptionHistory.propTypes = {
  audits: PropTypes.array.isRequired,
};



