import React from 'react';
import k from 'app/constants';
import { Router, Route, browserHistory } from 'react-router';
import { locationChange } from 'app/actions/routing';
import store from 'app/stores/store';

import App from 'app/scenes/app';

// /hosting
import Hosting from 'app/scenes/hosting';
import SubscriptionsList from 'app/scenes/hosting/subscriptions/list';
import SubscriptionsShow from 'app/scenes/hosting/subscriptions/show';
import SubscriptionsNew from 'app/scenes/hosting/subscriptions/new';
import SubscriptionsEdit from 'app/scenes/hosting/subscriptions/edit';

// /tele
import Tele from 'app/scenes/tele';
import TeleAgreementList from 'app/scenes/tele/agreements/list';
import TeleAgreementShow from 'app/scenes/tele/agreements/show';
import TeleAgreementEdit from 'app/scenes/tele/agreements/edit';
import TeleAgreementNew from 'app/scenes/tele/agreements/new';

// External targets
import external from 'ui/external';
const TimeLogLink = external('https://nav.gowingu.net:8091/TimeLogImportDF.aspx');
const WHMCSLink = external('https://nav.gowingu.net:8091/WHMCSImportDF.aspx');

// Listen for changes in the browser history, and update the routing
// application state.
browserHistory.listen(location => {
  store.dispatch(locationChange(location));
});

// The default routes configuration.
// This could also be split up into public and private routes.
const routes = (
  <Router history={browserHistory}>
    <Route path={k.BASEURL} component={App}>
      <Route path="hosting" component={Hosting}>
        <Route path="subscriptions" component={SubscriptionsList} />
        <Route path="subscriptions/new" component={SubscriptionsNew} />
        <Route path="subscriptions/:id" component={SubscriptionsShow} />
        <Route path="subscriptions/:id/edit" component={SubscriptionsEdit} />
      </Route>

      <Route path="tele" component={Tele}>
        <Route path="agreements" component={TeleAgreementList} />
        <Route path="agreements/new" component={TeleAgreementNew} />
        <Route path="agreements/:id" component={TeleAgreementShow} />
        <Route path="agreements/:id/edit" component={TeleAgreementEdit} />
      </Route>

      <Route path={`${k.BASEURL}timelog`} component={TimeLogLink} />
      <Route path={`${k.BASEURL}whmcs`} component={WHMCSLink} />
    </Route>
  </Router>
);

// Export the routes object
export default routes;



